import ReactDOM from 'react-dom'
import { StrictMode } from 'react'
import App from './App'
import { UserProvider, AuthProvider } from './context'

ReactDOM.render(
  <StrictMode>
    <AuthProvider>
      <UserProvider>
        <App />
      </UserProvider>
    </AuthProvider>
  </StrictMode>,
  document.getElementById('root')
)
