import styled from 'styled-components'
import { Theme } from 'styles/Themes'

export const Container = styled.div`
  display: flex;
  position: relative;

  gap: 24px;

  margin-left: 32px;

  .customer-span {
    max-height: 23px;
    color: ${Theme.pallete.QUATERNARY};
    font-family: ${Theme.fontFamily.PRIMARY};
    font-size: 14px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    letter-spacing: 0.14px;
  }

  .customer-h1 {
    color: ${Theme.pallete.PRIMARY};
    font-family: ${Theme.fontFamily.PRIMARY};
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    max-height: 23px;
    margin: 20px 0px 40px 0px;
  }

  .customer-btn {
    margin-bottom: 20px;
  }

  .customer-submenu {
    width: 180px;
  }

  .customer-kanban {
    padding: 107px 0px 0px 0px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 16px;
  }
`
