/* eslint-disable react/no-unused-prop-types */
import CircleChart from 'components/CircleChart'
import { ccb, whiteFlag } from 'assets/Icons'
import { IInvestCard } from 'interfaces'
import { useContext } from 'react'
import { UserContext } from 'context/userContext'
import * as Style from './style'

interface IInvestCardProps {
  item?: IInvestCard
  i?: number
  disabled?: boolean
  onClick: any
  legend: string
  colorTags?: string[]
  opacity?: number
}

const ImageContent = ({ item }: IInvestCardProps) => {
  return (
    <Style.ImageContainer>
      <Style.Gradient opacity={item?.opacity}>
        <Style.ImageContent>
          <Style.MinRow start>
            {item?.colorTags.map((tag: string) => {
              return (
                <Style.ColoredTag
                  color={item?.color}
                  key={`color-tag-poster-${tag}`}
                >
                  <Style.TagText>{tag}</Style.TagText>
                </Style.ColoredTag>
              )
            })}
          </Style.MinRow>

          <Style.MinRow start>
            <img className="svg-card-icon" src={ccb} alt="card icon" />
            <Style.InvestTitle>{`${item?.name} / ${item?.createdAt}`}</Style.InvestTitle>
          </Style.MinRow>
        </Style.ImageContent>
      </Style.Gradient>
    </Style.ImageContainer>
  )
}

const CardContent = ({ item, legend }: IInvestCardProps) => {
  return (
    <Style.CardContainer>
      {item?.chart && (
        <CircleChart radius={75} valueArray={item?.chart} strokeWidth={3}>
          <Style.RateColoured color={item?.color}>
            {item?.coloredChartTitle}
          </Style.RateColoured>
          <Style.RateText>{item?.blackChartTitle}</Style.RateText>
        </CircleChart>
      )}

      <Style.MinRow center>
        <Style.InvestText>{legend}</Style.InvestText>
        <Style.MoneyText>{`${item?.min}`}</Style.MoneyText>
      </Style.MinRow>
    </Style.CardContainer>
  )
}

const InvestCard = ({
  item,
  i,
  onClick,
  disabled = false,
  legend = 'Investimento mínimo:',
}: IInvestCardProps) => {
  const { setSelectedCardId } = useContext(UserContext)
  return (
    <Style.Container color={item?.color} key={`label_poster_card_${i}`}>
      {item?.guarantee && (
        <>
          <Style.FlagIcon src={whiteFlag} alt="Flag icon" />
          <Style.FlagText color={item?.color}>{item?.guarantee}</Style.FlagText>
        </>
      )}
      <Style.Card
        onClick={() => !disabled && setSelectedCardId(item?.id) && onClick}
      >
        <>
          <ImageContent
            item={item}
            onClick={setSelectedCardId(item?.id) && onClick}
            legend={legend}
          />
          <CardContent
            item={item}
            disabled={disabled}
            legend={legend}
            colorTags={item?.colorTags}
            i={0}
            onClick={setSelectedCardId(item?.id) && onClick}
          />
        </>
      </Style.Card>
    </Style.Container>
  )
}

export default InvestCard
