/* eslint-disable consistent-return */
/* eslint-disable array-callback-return */
/* eslint-disable react/button-has-type */
import { columnSorting, picDashboard } from 'assets/Icons'
import { IPerson } from 'interfaces'
import { Icon } from 'assets/index'
import { useState } from 'react'
import { TableContainer } from './style'

interface IGridProps {
  data: IPerson[]
}

export default function DataGridDemo(props: IGridProps) {
  const { data } = props
  const [firstItemPage, setFirstItemPage] = useState<number>(0)
  const [page, setPage] = useState<number>(1)

  const advanceTablePage = () => {
    if (page === Math.ceil(data.length / 10)) return
    setFirstItemPage((oldValue) => oldValue + 10)
    setPage((oldValue) => oldValue + 1)
  }

  const backTablePage = () => {
    if (page === 1) return
    setFirstItemPage((oldValue) => oldValue - 10)
    setPage((oldValue) => oldValue - 1)
  }

  const counter = (level: number[]) => {
    let count = 0
    level.forEach((permission) => {
      if (permission === 1) count += 1
    })
    return count
  }

  if (!data.length) return null

  return (
    <TableContainer>
      <button
        onClick={() => console.log('sorting')}
        className="grid-header border-top-left"
      >
        <span>#</span>
        <img
          className="btn-next-icon"
          src={columnSorting}
          alt="column sorting"
        />
      </button>
      <button onClick={() => console.log('sorting')} className="grid-header">
        <span>COLABORADOR</span>
        <img
          className="btn-next-icon"
          src={columnSorting}
          alt="column sorting"
        />
      </button>
      <button onClick={() => console.log('sorting')} className="grid-header">
        <span>NÍVEL DE ACESSO</span>
        <img
          className="btn-next-icon"
          src={columnSorting}
          alt="column sorting"
        />
      </button>
      <button onClick={() => console.log('sorting')} className="grid-header">
        <span>COMISSIONAMENTO</span>
        <img
          className="btn-next-icon"
          src={columnSorting}
          alt="column sorting"
        />
      </button>
      <button onClick={() => console.log('sorting')} className="grid-header">
        <span>EMAIL</span>
        <img
          className="btn-next-icon"
          src={columnSorting}
          alt="column sorting"
        />
      </button>
      <button className="grid-header">
        <span>TELEFONE</span>
      </button>
      <button onClick={() => console.log('sorting')} className="grid-header">
        <span>STATUS</span>
        <img
          className="btn-next-icon"
          src={columnSorting}
          alt="column sorting"
        />
      </button>
      <button className="grid-header border-top-right">
        <span>AÇÃO</span>
      </button>
      {data.map((user, index) => {
        if (index >= firstItemPage && index < firstItemPage + 10)
          return (
            <>
              <div className="grid-item">{index + 1}</div>
              <div className="grid-item-start">
                <img src={picDashboard} alt="card icon" />
                <span>{user.name}</span>
              </div>
              <div className="grid-item">{`Acesso: ${counter(
                user.level
              )}`}</div>
              <div className="grid-item">-</div>
              <div className="grid-item-start">{user.email}</div>
              <div className="grid-item">{user.cellphone}</div>
              <div className="grid-item">{user.status}</div>
              <div className="grid-action">
                <img src={Icon('edit.svg')} alt="column sorting" />
                <img src={Icon('trash.svg')} alt="column sorting" />
              </div>
            </>
          )
      })}
      <div className="grid-footer border-bottom-left">{`${
        firstItemPage > 0 ? `${firstItemPage + 1}` : '1'
      }-${
        firstItemPage + 10 > data.length ? data.length : firstItemPage + 10
      } de ${data.length}`}</div>
      <div className="grid-footer" />
      <div className="grid-footer" />
      <div className="grid-footer" />
      <div className="grid-footer" />
      <div className="grid-footer" />
      <div className="grid-footer">{`Página ${page}`}</div>
      <div className="grid-footer border-bottom-right">
        <img
          className="table-chevron"
          src={Icon('chevron_left.svg')}
          alt="back table page"
          onClick={backTablePage}
        />
        <span>{`${page}/${Math.ceil(data.length / 10)}`}</span>
        <img
          className="table-chevron"
          src={Icon('chevron_right.svg')}
          alt="advance table page"
          onClick={advanceTablePage}
        />
      </div>
    </TableContainer>
  )
}
