import { useContext } from 'react'
import { Icon } from 'assets/index'
import { UserContext } from 'context/userContext'
import { ButtonNext } from './style'

interface IButtonNextProps {
  isBack: boolean
  isAdvance: boolean
  advancePage: () => void
  backPage: () => void
}

export default function BtnNext(props: IButtonNextProps) {
  const { isBack, isAdvance, advancePage, backPage } = props
  const { allowNextPage, setAllowNextPage } = useContext(UserContext)
  return (
    <ButtonNext allowNextPage={allowNextPage} {...props}>
      {isAdvance ? null : (
        <div className="btn-back-circle">
          <img
            className="btn-next-icon"
            onClick={backPage}
            src={Icon('chevron_left.svg')}
            alt="back button"
          />
        </div>
      )}
      {isBack ? 'VOLTAR' : 'AVANÇAR'}
      {isBack ? null : (
        <div className="btn-next-circle">
          <img
            className="btn-next-icon"
            onClick={
              allowNextPage ? advancePage : () => setAllowNextPage(false)
            }
            src={Icon('chevron_right.svg')}
            alt="advance button"
          />
        </div>
      )}
    </ButtonNext>
  )
}
