import styled from 'styled-components'
import { Theme } from 'styles/Themes'
import img from './carrao.png'

interface IStyleProps {
  img?: string
  opacity?: number
  color?: string
  center?: boolean
  start?: boolean
  regular?: boolean
}

export const Container = styled.div`
  width: 304px;
  height: 376px;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
`

export const Card = styled.div`
  width: 300px;
  height: 372px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  background: #fff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25);
`

export const CardContainer = styled.div`
  width: 100%;
  height: 246px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
`

export const ImageContainer = styled.div`
  background-image: url(${img});
  width: 300px;
  height: 126px;
  z-index: 1;
  position: relative;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  overflow: hidden;
`

export const Gradient = styled.div`
  width: 300px;
  height: 126px;
  position: absolute;
  z-index: 2;
  background-color: ${(props: IStyleProps) => {
    if (props.opacity) return `#000000${Number(props.opacity)}`
    return '#00000090'
  }};
  top: 0;
`

export const ImageContent = styled.div`
  width: 300px;
  height: 126px;
  position: absolute;
  z-index: 3;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 16px 12.5px 16px 12.5px;
`

export const FlagIcon = styled.img`
  position: absolute;
  top: 0;
  right: 0;
  z-index: 4;
`

export const FlagText = styled.span`
  position: absolute;
  white-space: nowrap;
  text-overflow: ellipsis;
  top: 14px;
  right: -8px;
  z-index: 4;
  font-weight: 700;
  transform: rotate(45deg);
  font-size: 8px;
  text-align: center;
  width: 55px;
  color: ${(props: IStyleProps) => {
    if (props.color) return props.color
    return Theme.pallete.PRIMARY
  }};
`

export const MinRow = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: ${(props: IStyleProps) => {
    if (props.center) return 'center'
    if (props.start) return 'flex-start'
    return 'space-between'
  }};
  align-items: center;
  width: 100%;

  .svg-card-icon {
    width: 24px;
    height: 24px;
  }
`

export const ColoredTag = styled.div`
  background-color: ${(props) => {
    if (props.color) return props.color
    return '#9E9E9E'
  }};
  padding: 3px 8px;
  border-radius: 100px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-right: 8px;
`

export const TagText = styled.span`
  font-size: 10px;
  font-weight: ${(props: IStyleProps) => {
    if (props.regular) return '400'
    return '700'
  }};
  color: ${Theme.pallete.PRIMARY};
`

export const InvestTitle = styled.span`
  color: ${Theme.pallete.SECONDARY};
  font-weight: 700;
  line-height: 20px;
  font-size: 16px;
  margin: 0 0 0 8px;
`

export const InvestText = styled.span`
  font-size: 12px;
  font-weight: 400;
  color: ${Theme.pallete.PRIMARY};
`

export const MoneyText = styled.span`
  font-weight: 700;
  font-size: 16px;
  color: ${Theme.pallete.PRIMARY};
  margin-left: 16px;
`

export const RateColoured = styled.span`
  color: ${(props) => {
    if (props.color) return props.color
    return `${Theme.pallete.PRIMARY}`
  }};
  font-weight: 700;
  font-size: 14px;
`

export const RateText = styled.span`
  color: ${Theme.pallete.PRIMARY};
  font-weight: 300;
  font-size: 10px;
`
