import { Unauthorized } from 'layouts/index'
import { useState, useRef } from 'react'
import { LoginContainer } from 'styles/index'
import { Form } from '@unform/web'
import { logo } from 'assets/Icons'
import {
  DefaultButton,
  Feedback,
  Input,
  Loading,
  RecoverPass,
} from 'components/index'
import useAuth from 'hooks/useAuth'
import { useNavigate } from 'react-router-dom'
import useFetch from 'hooks/useFetch'

export default function Login() {
  const [loading, setLoading] = useState(false)
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  const auth = useAuth()

  const navigate = useNavigate()
  const from = '/dashboard'
  const formRef = useRef<HTMLFormElement | any>(null)

  const api = useFetch()

  const handleLogin = async (data: { email: string; password: string }) => {
    try {
      setLoading(true)
      const response = await api.fetch('post', data, '/gestor/login')
      if (response.fail) throw new Error(response.errors.message)
      setLoading(false)

      await auth.signin(response.data.token, () => {
        navigate(from, { replace: true })
      })
    } catch (err: any) {
      setLoading(false)
      console.log('err', err)
    }
  }

  if (loading) return <Loading />
  return (
    <Unauthorized>
      <LoginContainer>
        <img className="login-company-logo" src={logo} alt="company Logo" />
        <span className="login-title">Acessar plataforma de administração</span>
        <Form
          onSubmit={() => console.log('submit')}
          className="login-form"
          autoComplete="off"
          ref={formRef}
        >
          <Input
            onChange={(e: any) => setEmail(e.target.value)}
            name="email"
            type="text"
            placeholder="Email"
            value={email}
          />
          <Input
            onChange={(e: any) => setPassword(e.target.value)}
            name="password"
            type="password"
            placeholder="Senha"
            value={password}
          />
          <DefaultButton
            $loading={false}
            onClick={() =>
              handleLogin({
                email,
                password,
              })
            }
          >
            Entrar
          </DefaultButton>
          <RecoverPass to="/demo">Esqueceu a senha?</RecoverPass>
        </Form>
      </LoginContainer>
    </Unauthorized>
  )
}
