import styled from 'styled-components'
import { Theme } from 'styles/Themes'
import { IMaskInput } from 'react-imask'

interface IStyleProps {
  rounded?: boolean
  small?: boolean
  xsmall?: boolean
  large?: boolean
  icon?: boolean
  error?: string
  smallPad?: boolean
  disabled?: boolean
  resumeCard?: boolean
  empty?: boolean
}

export const Span = styled.span`
  position: absolute;
  left: 11px;
  top: -11px;

  color: ${Theme.pallete.PRIMARY4};
  font-family: ${Theme.fontFamily.SECONDARY};
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

  height: 20px;

  padding: 2px 6px;
  border-radius: 10px;

  background: ${Theme.pallete.SECONDARY};

  opacity: ${(props: IStyleProps) => (props.empty ? '0' : '1')};
`

export const MaskedInput = styled(IMaskInput)`
  position: ${(props: IStyleProps) => (props.icon ? 'relative' : 'inherit')};
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;

  height: 48px;
  width: ${(props: IStyleProps) => {
    if (props.xsmall) return '139px'
    if (props.small) return '150px'
    if (props.large) return '200px'
    return '315px'
  }};

  max-width: ${(props: IStyleProps) => {
    if (props.xsmall) return '139px'
    if (props.small) return '150px'
    if (props.large) return '200px'
    return '315px'
  }};

  padding: 20px 50px 20px 20px;

  &:hover {
    background: ${(props: IStyleProps) =>
      props.error ? '#ff0000' : '#ffffff'};
  }

  box-sizing: border-box;
  border: ${(props: IStyleProps) =>
    props.error ? '1px solid #f60665' : '1px solid #cccccc'};
  border-radius: ${(props: IStyleProps) => (props.rounded ? '30px' : '5px')};
  background: ${(props: IStyleProps) => (props.error ? '#FF0000' : '#ffffff')};

  &:focus {
    &::placeholder {
      color: transparent;
      opacity: 0;
    }
  }

  &:disabled {
    &::placeholder {
      color: white;
    }
    background: lightGray;
    border: 0;
  }
`

export const InputIcon = styled.img`
  width: ${(props: { icon: string }) => {
    if (props.icon === 'password') return '16px'
    return '20px'
  }};

  height: ${(props: { icon: string }) => {
    if (props.icon === 'password') return '16px'
    return '20px'
  }};

  filter: invert(48%) sepia(5%) saturate(92%) hue-rotate(98deg) brightness(93%)
    contrast(97%);

  position: absolute;
  right: 25px;
  top: 17px;
`

export const InputError = styled.div`
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: start;
  padding: 5px 20px;
  width: 215px;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #f60665;
`
