import styled from 'styled-components'

export const Container = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;

  gap: 20px;

  margin-left: 32px;

  .financial-kanban {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 16px;
  }

  .financial-path {
    color: #868686;

    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    letter-spacing: 0.14px;
  }

  .financial-title {
    color: #000;

    /* H1 - Títulos */
    font-family: Roboto;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    margin-bottom: 32px;
  }

  .financial-body-container {
    display: flex;

    gap: 24px;
  }

  .financial-left-container {
    display: flex;
    flex-direction: column;

    gap: 20px;
  }
`
