// import { MouseEvent } from 'react'
import { FileInput } from 'components'
import { Icon } from 'assets/index'
import UploadImg from './style'

interface IUploadImgProps {
  children?: string
}

export default function uploadImg(props: IUploadImgProps) {
  const { children } = props
  return (
    <UploadImg htmlFor="uploadImg">
      <img className="ui-img" src={Icon('image.svg')} alt="img" />
      <span className="upload-text">{children}</span>
      <span className="upload-info">Máx: 10MB</span>
      <FileInput className="ui-input" id="uploadImg" name="uploadImg" />
      <img className="ui-img" src={Icon('cloud.svg')} alt="cloud" />
    </UploadImg>
  )
}
