import {
  ResumeInputCard,
  ResumePictureCard,
  SelectCard,
} from 'components/index'
import { Icon } from 'assets/index'
import { useContext, useEffect } from 'react'
import { UserContext } from 'context/userContext'
import * as Yup from 'yup'
import { Container } from './style'

const schemaNewGestor = Yup.object().shape({
  name: Yup.string().required('campo obrigatório: nome'),
  document: Yup.string().min(14).max(14).required('campo obrigatório: CPF'),
  email: Yup.string().required('campo obrigatório: email'),
  cellphone: Yup.string()
    .min(15)
    .max(15)
    .required('campo obrigatório: telefone'),

  level: Yup.array()
    .of(Yup.number())
    .length(6)
    .required('campo inválido: valores dos acessos estão inválidos'),
  profilePic: Yup.string().required('campo obrigatório: foto de perfil'),
})

export default function ResumoColaborador() {
  const { gestor, setGestor, setAllowNextPage } = useContext(UserContext)

  const counter = (level: number[] | undefined) => {
    let count = 0
    level?.forEach((permission) => {
      if (permission === 1) count += 1
    })
    return count
  }

  const choseOption = (e: any) => {
    const levelLength = counter(gestor?.level)
    if (levelLength <= 1) return

    const id = e.target.innerText
    let index: number | null = null

    if (id === 'Especialista') index = 0
    if (id === 'Gestor') index = 1
    if (id === 'Financeiro') index = 2
    if (id === 'Jurídico') index = 3
    if (id === 'RI') index = 4
    if (id === 'Gestão usuários') index = 5
    if (index === null) return

    const newLevel: any = gestor?.level
    newLevel[index] === 1 ? (newLevel[index] = 0) : (newLevel[index] = 1)

    setGestor((curr: any) => {
      return { ...curr, level: newLevel }
    })
  }

  useEffect(() => {
    setAllowNextPage(false)
    const validateInputs = async () => {
      try {
        await schemaNewGestor.validate(gestor, { abortEarly: false })
        setAllowNextPage(true)
      } catch (err) {
        console.log(err)
        setAllowNextPage(false)
      }
    }
    validateInputs()
  }, [gestor])

  return (
    <Container>
      <div className="new-colaborator-container">
        <h1 className="new-colaborator-title">Resumo Novo Colaborador</h1>
        <span className="new-colaborator-instruction">
          Confira e avance na seta abaixo para confirmar a criação
        </span>
      </div>
      <div className="new-colaborator-container">
        <ResumeInputCard
          icon="profile.svg"
          placeholder={gestor?.name || ''}
          names="name"
          handleChangeInput={(e: any) => {
            setGestor((oldValue: any) => ({
              ...oldValue,
              name: e,
            }))
          }}
        >
          Nome do colaborador
        </ResumeInputCard>
        <ResumeInputCard
          icon="profile.svg"
          placeholder={gestor?.document || ''}
          names="cpf"
          isMask="000.000.000-00"
          handleChangeInput={(e: any) => {
            setGestor((oldValue: any) => ({
              ...oldValue,
              document: e,
            }))
          }}
        >
          CPF
        </ResumeInputCard>
        <ResumeInputCard
          icon="mail.svg"
          placeholder={gestor?.email || ''}
          names="email"
          handleChangeInput={(e: any) => {
            setGestor((oldValue: any) => ({
              ...oldValue,
              email: e,
            }))
          }}
        >
          Email
        </ResumeInputCard>
        <ResumeInputCard
          icon="profile.svg"
          placeholder={gestor?.cellphone || ''}
          names="phoneNumber"
          handleChangeInput={(e: any) => {
            setGestor((oldValue: any) => ({
              ...oldValue,
              cellphone: e,
            }))
          }}
        >
          Número de celular
        </ResumeInputCard>
        <ResumePictureCard name="edit picture" title="Foto Neowinner" />
      </div>
      <div className="new-colaborator-subtitle">
        <img
          className="iconClickable"
          src={Icon('safety.svg')}
          alt="subtitle img"
        />
        <h4>Acessos permitidos</h4>
      </div>
      <div className="new-colaborator-container">
        {gestor?.level?.map((lvl: number, index: number) => {
          if (lvl === 1)
            return (
              <SelectCard
                icon={
                  index === 0
                    ? 'profile.svg'
                    : index === 1
                    ? 'users.svg'
                    : index === 2
                    ? 'dollar.svg'
                    : index === 3
                    ? 'file.svg'
                    : index === 4
                    ? 'volume.svg'
                    : 'settings.svg'
                }
                value={
                  index === 0
                    ? 'Especialista'
                    : index === 1
                    ? 'Gestor'
                    : index === 2
                    ? 'Financeiro'
                    : index === 3
                    ? 'Jurídico'
                    : index === 4
                    ? 'RI'
                    : 'Gestão usuários'
                }
                onClick={(e: any) => choseOption(e)}
                checked={gestor?.level[index] === 1}
              />
            )
          return null
        })}
      </div>
    </Container>
  )
}
