import { search } from 'assets/Icons'
import { Container, SearchBar } from './style'

interface ISearchBarComponentProps {
  large?: boolean
  onChange?: any
}

export default function SearchBarComponent(props: ISearchBarComponentProps) {
  const { large, onChange } = props
  return (
    <Container onChange={onChange} {...props}>
      <img className="sb-img" src={search} alt="search icon" />
      <SearchBar
        placeholder={large ? 'Pesquisar colaborador' : 'CPF / CNPJ Cliente'}
      />
    </Container>
  )
}
