import { Unauthorized } from 'layouts/index'
import { useState, useRef, useEffect } from 'react'
import { LoginContainer } from 'styles/index'
import { Form } from '@unform/web'
import { logo } from 'assets/Icons'
import { DefaultButton, Input, Loading } from 'components/index'
import * as Yup from 'yup'
import { useLocation, useNavigate } from 'react-router-dom'
import useFetch from 'hooks/useFetch'

const schemaNewPassword = Yup.object().shape({
  password: Yup.string().required(),
  confirmPassword: Yup.string().oneOf(
    [Yup.ref('password'), null],
    'Senhas devem combinar'
  ),
})

export default function CreatePassword() {
  const [loading, setLoading] = useState<boolean>(false)
  const [password, setPassword] = useState<string>('')
  const [confirmPassword, setConfirmPassword] = useState<string>('')
  const [token, setToken] = useState<string>('')

  const navigate = useNavigate()
  const url = useLocation()
  const formRef = useRef<HTMLFormElement | any>(null)

  const api = useFetch()

  const handleCreatePassword = async (data: {
    password: string
    confirmPassword: string
    token: string
  }) => {
    try {
      setLoading(true)
      const response = await api.fetch('put', data, '/gestor/password')
      console.log('response', response)
      if (response.fail) throw new Error(response.errors.message)
      setLoading(false)
      navigate('/login', { replace: true })
    } catch (err: any) {
      setLoading(false)
      console.log('err', err)
    }
  }

  useEffect(() => {
    setToken(url.search.split('token=')[1])
    const validateInputs = async () => {
      try {
        await schemaNewPassword.validate(
          { password, confirmPassword },
          { abortEarly: false }
        )
      } catch (err) {
        console.log(err)
      }
    }
    validateInputs()
  }, [password, confirmPassword])

  if (loading) return <Loading />
  return (
    <Unauthorized>
      <LoginContainer>
        <img className="login-company-logo" src={logo} alt="company Logo" />
        <span className="login-title">Crie senha para acesso</span>
        <span className="login-subtitle">
          Ela deve ter apenas números e, no mínimo, 8 dígitos.
        </span>
        <Form
          onSubmit={() => console.log('submit')}
          className="login-form"
          autoComplete="off"
          ref={formRef}
        >
          <Input
            onChange={(e: any) => setPassword(e.target.value)}
            name="password"
            type="password"
            placeholder="Criar senha"
            value={password}
          />
          <Input
            onChange={(e: any) => setConfirmPassword(e.target.value)}
            name="confirmPassword"
            type="password"
            placeholder="Confirmar senha"
            value={confirmPassword}
          />
          <DefaultButton
            $loading={false}
            onClick={() =>
              handleCreatePassword({
                password,
                confirmPassword,
                token,
              })
            }
          >
            Criar senha
          </DefaultButton>
        </Form>
      </LoginContainer>
    </Unauthorized>
  )
}
