import { UserContext } from 'context'
import useAuth from 'hooks/useAuth'
import { useContext } from 'react'
import { useLocation } from 'react-router-dom'
import { hideMenu, logo, logoMenu, logoSmall } from 'assets/Icons'
import { Icon } from 'assets/index'
import { Menu } from './style'
import Button from './ButtonMenu'

export default function MenuComponent() {
  const { extended, setExtended } = useContext(UserContext)
  const auth = useAuth()
  const location = useLocation()

  const handleLogout = () => {
    auth.signout(() => console.log('logout'))
  }

  const handleExtendMenu = () => {
    setExtended((oldValue) => !oldValue)
  }

  return (
    <Menu extended={extended}>
      <div>
        <img
          className="extendButton"
          src={hideMenu}
          onClick={handleExtendMenu}
          onKeyDown={handleExtendMenu}
          alt="extend menu"
        />

        {extended ? (
          <img className="logo" src={logo} alt="card icon" />
        ) : (
          <img className="logo" src={logoMenu} alt="card icon" />
        )}
        <Button
          extended={extended}
          icon={Icon('dashboard.svg')}
          name="dashboard"
          active={/dashboard/i.test(location.pathname)}
          to="dashboard"
        >
          Dashboard
        </Button>
        <Button
          extended={extended}
          icon={Icon('profile.svg')}
          name="clientes"
          active={/clientes/i.test(location.pathname)}
          to="clientes"
        >
          Gestão de clientes
        </Button>
        <Button
          extended={extended}
          icon={Icon('building.svg')}
          name="produtos"
          active={/produtos/i.test(location.pathname)}
          to="produtos"
        >
          Gestão de produtos
        </Button>
        <Button
          extended={extended}
          icon={Icon('users.svg')}
          name="colaboradores"
          active={/colaboradores/i.test(location.pathname)}
          to="colaboradores"
        >
          Gestão de Colaboradores
        </Button>
        <Button
          extended={extended}
          icon={Icon('trophy.svg')}
          name="financeiro"
          active={/financeiro/i.test(location.pathname)}
          to="financeiro"
        >
          Financeiro
        </Button>
        <Button
          extended={extended}
          icon={Icon('cast_banner.svg')}
          name="banner"
          active={/banner/i.test(location.pathname)}
          to="banner"
        >
          Banner
        </Button>
      </div>

      <Button
        logoutButton
        extended={extended}
        icon={Icon('logout.svg')}
        name="Logout"
        onClick={handleLogout}
        to="login"
      >
        Log out
      </Button>
    </Menu>
  )
}
