import { RefObject } from 'react'
import styled from 'styled-components'
import { Theme } from 'styles/Themes'

interface IStyleProps {
  disabled?: boolean
  ref?: (
    _instance: HTMLDivElement | null
  ) => void | RefObject<HTMLDivElement> | null
  isDragging?: boolean
  color: string
  background: string
}

interface ITagProps {
  background?: string
  color?: string
}

export const KanbanCard = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  height: auto;
  min-height: 142px;
  max-height: 172px;

  width: 210px;
  padding: 10px;
  gap: 8px;

  line-height: 16px;
  user-select: none;

  border-radius: 5px;
  border: none;

  cursor: ${(props: IStyleProps) => {
    if (props.isDragging) return 'grabbing'
    return props.disabled ? 'pointer' : 'grab'
  }};
  box-shadow: ${(props: IStyleProps) =>
    props.isDragging && `0px 3px 4px 0px ${Theme.pallete.PRIMARY}`};

  color: ${(props: IStyleProps) => {
    return props.color || '#ffffff'
  }};
  background: ${(props: IStyleProps) => {
    return props.background || '#fdaB3d'
  }};

  .kc-name {
    height: 16px;
    color: ${(props: IStyleProps) => {
      return props.color || '#ffffff'
    }};
    font-family: ${Theme.fontFamily.SECONDARY};
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.14px;
  }

  .kc-tags-container {
    display: flex;
    height: auto;
    flex-wrap: wrap;

    gap: 6px;

    overflow: hidden;
  }

  .kc-specialist {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 190px;
    height: 32px;

    border-radius: 5px;

    color: ${Theme.pallete.PRIMARY};
    font-family: ${Theme.fontFamily.SECONDARY};
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
    background: ${Theme.pallete.SECONDARY};

    .kc-icon {
      max-width: 16px;
      max-height: 16px;
      margin-right: 8px;
    }

    .kc-span {
      max-width: 150px;
      max-height: 14px;
      color: ${Theme.pallete.PRIMARY};
      font-family: ${Theme.fontFamily.SECONDARY};
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 14px;
    }
  }
`

export const Tag = styled.span`
  text-align: center;

  width: auto;
  height: auto;

  border-radius: 5px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.1px;

  padding: 2px 6px;

  color: ${(props: ITagProps) => {
    return props.color || '#ffffff'
  }};
  background: ${(props: ITagProps) => {
    return props.background || '#00cd52'
  }};
`
