import styled from 'styled-components'
import { Theme } from 'styles/Themes'

interface IStyleProps {
  border?: boolean
  small?: boolean
  secondary?: boolean
}

export default styled.button`
  display: flex;
  align-items: center;
  justify-content: center;

  width: ${(props: IStyleProps) => {
    if (props.small) return '180px'
    return '315px'
  }};
  height: 50px;

  border: ${(props: IStyleProps) => {
    if (props.border) return ''
    return 'none'
  }};

  font-family: ${Theme.fontFamily.SECONDARY};

  cursor: pointer;

  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;

  border-radius: 5px;

  background: ${(props: IStyleProps) => {
    if (props.secondary) return `${Theme.pallete.PRIMARY3}`
    return `${Theme.pallete.TERTIARY}`
  }};

  color: ${(props: IStyleProps) => {
    if (props.secondary) return `${Theme.pallete.SECONDARY2}`
    return `${Theme.pallete.PRIMARY}`
  }};

  &:hover {
    background: ${(props: IStyleProps) => {
      if (props.secondary) return `${Theme.pallete.PRIMARY5}`
      return `${Theme.pallete.TERTIARY2}`
    }};
  }

  &:focus {
    fill: ${(props: IStyleProps) => {
      if (props.secondary) return `${Theme.pallete.PRIMARY5}`
      return `${Theme.pallete.TERTIARY2}`
    }};
    stroke-width: 1px;
    stroke: ${(props: IStyleProps) => {
      if (props.secondary) return `${Theme.pallete.PRIMARY5}`
      return `${Theme.pallete.TERTIARY2}`
    }};
    box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.25);
    background: ${(props: IStyleProps) => {
      if (props.secondary) return `${Theme.pallete.PRIMARY3}`
      return `${Theme.pallete.TERTIARY2}`
    }};
  }

  &:disabled {
    background: ${Theme.pallete.QUATERNARY};
    color: ${Theme.pallete.SECONDARY2};
    cursor: not-allowed;
  }

  .default-btn-img {
    max-width: 24px;
    max-height: 24px;
    margin-right: 8px;
  }
`
