import { Icon } from 'assets/index'
import { useContext, useEffect, useRef } from 'react'
import { Form } from '@unform/web'
import { MaskedInput, Select, SelectCard, UploadImg } from 'components/index'
import { UserContext } from 'context/userContext'
import * as Yup from 'yup'
import { Container } from './style'

const schemaNewGestor = Yup.object().shape({
  name: Yup.string().required('campo obrigatório: nome'),
  document: Yup.string().min(14).max(14).required('campo obrigatório: CPF'),
  email: Yup.string().required('campo obrigatório: email'),
  cellphone: Yup.string()
    .min(15)
    .max(15)
    .required('campo obrigatório: telefone'),

  level: Yup.array()
    .of(Yup.number())
    .length(6)
    .required('campo inválido: valores dos acessos estão inválidos'),
  profilePic: Yup.string().required('campo obrigatório: foto de perfil'),
})

export default function NovoColaborador() {
  const { gestor, setGestor, setAllowNextPage } = useContext(UserContext)
  const formRef = useRef<HTMLFormElement | any>(null)

  const choseOption = (e: any) => {
    const id = e.target.innerText
    let index: number | null = null

    if (id === 'Especialista') index = 0
    if (id === 'Gestor') index = 1
    if (id === 'Financeiro') index = 2
    if (id === 'Jurídico') index = 3
    if (id === 'RI') index = 4
    if (id === 'Gestão usuários') index = 5
    if (index === null) return

    const newLevel: any = gestor?.level
    newLevel[index] === 1 ? (newLevel[index] = 0) : (newLevel[index] = 1)

    setGestor((curr: any) => {
      return { ...curr, level: newLevel, profilePic: 'profile.svg' }
    })
  }

  useEffect(() => {
    setAllowNextPage(false)
    const validateInputs = async () => {
      try {
        await schemaNewGestor.validate(gestor, { abortEarly: false })
        setAllowNextPage(true)
      } catch (err) {
        console.log(err)
        setAllowNextPage(false)
      }
    }
    validateInputs()
  }, [gestor])

  return (
    <Container>
      <Form ref={formRef} onSubmit={() => console.log('submit')}>
        <div>
          <h1 className="new-colaborator-title">Novo Colaborador</h1>
          <div className="new-colaborator-container">
            <MaskedInput
              smallPad
              type="text"
              handleChange={(e: any) => {
                setGestor((oldValue: any) => ({
                  ...oldValue,
                  name: e.target.value,
                }))
              }}
              placeholder="Nome do colaborador"
              name="name"
              defaultValue={gestor?.name}
            />
            <MaskedInput
              smallPad
              type="cpf"
              handleChange={(e: any) => {
                setGestor((oldValue: any) => ({
                  ...oldValue,
                  document: e.target.value,
                }))
              }}
              placeholder="CPF"
              name="cpf"
              defaultValue={gestor?.document}
            />
            <MaskedInput
              smallPad
              type="text"
              handleChange={(e: any) => {
                setGestor((oldValue: any) => ({
                  ...oldValue,
                  email: e.target.value,
                }))
              }}
              placeholder="E-mail"
              name="email"
              defaultValue={gestor?.email}
            />
            <MaskedInput
              smallPad
              type="cell"
              handleChange={(e: any) => {
                setGestor((oldValue: any) => ({
                  ...oldValue,
                  cellphone: e.target.value,
                }))
              }}
              placeholder="Número de celular"
              name="phoneNumber"
              defaultValue={gestor?.cellphone}
            />
            <UploadImg>Upload de foto</UploadImg>
          </div>
        </div>
        <div>
          <div className="new-colaborator-subtitle">
            <img
              className="new-colaborator-subtitle-img"
              src={Icon('trophy.svg')}
              alt="subtitle icon"
            />
            <h4>Programa de comissionamento</h4>
          </div>

          <div className="new-colaborator-container">
            <Select value={'Não'} name="comission" disabled />
          </div>
        </div>
        <div>
          <div className="new-colaborator-subtitle">
            <img
              className="new-colaborator-subtitle-img"
              src={Icon('safety.svg')}
              alt="subtitle icon"
            />
            <h4>Limites de acesso</h4>
          </div>

          <div className="new-colaborator-container-access">
            <SelectCard
              icon="profile.svg"
              value="Especialista"
              onClick={(e: any) => choseOption(e)}
              checked={gestor?.level[0] === 1}
            />
            <SelectCard
              icon="users.svg"
              value="Gestor"
              onClick={(e: any) => choseOption(e)}
              checked={gestor?.level[1] === 1}
            />
            <SelectCard
              icon="dollar.svg"
              value="Financeiro"
              onClick={(e: any) => choseOption(e)}
              checked={gestor?.level[2] === 1}
            />
            <SelectCard
              icon="file.svg"
              value="Jurídico"
              onClick={(e: any) => choseOption(e)}
              checked={gestor?.level[3] === 1}
            />
            <SelectCard
              icon="volume.svg"
              value="RI"
              onClick={(e: any) => choseOption(e)}
              checked={gestor?.level[4] === 1}
            />
            <SelectCard
              icon="settings.svg"
              value="Gestão usuários"
              onClick={(e: any) => choseOption(e)}
              checked={gestor?.level[5] === 1}
            />
          </div>
        </div>
      </Form>
    </Container>
  )
}
