import styled from 'styled-components'
import { IMaskInput } from 'react-imask'
import { Theme } from 'styles/Themes'

interface IStyleProps {
  small?: boolean
  green?: boolean
  secondary?: boolean
  disabled?: boolean
  medium?: boolean
}

export const ResMaskedInput = styled(IMaskInput)`
  border: none;
  max-width: ${(props: IStyleProps) => {
    if (props.small) return '138px'
    return '197px'
  }};
  background: ${(props: IStyleProps) => {
    if (props.green) return `${Theme.pallete.QUINARY}`
    if (props.secondary) return `${Theme.pallete.TERTIARY}`
    return `${Theme.pallete.SECONDARY}`
  }};
  cursor: ${(props: IStyleProps) => {
    if (props.disabled) return ''
    return 'pointer'
  }};
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  text-align: left;
  line-height: 21.09px;
  margin: 0 0 12px 12px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  ::placeholder {
    color: ${(props: IStyleProps) => {
      if (props.disabled) return `${Theme.pallete.PRIMARY}`
      return `${Theme.pallete.SECONDARY}`
    }};
  }
`

export const ResumeInputCard = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 142px;
  width: ${(props: IStyleProps) => {
    if (props.small) return '206px'
    if (props.medium) return '260px'
    return '315px'
  }};
  border-radius: 5px;
  box-shadow: 0px 3px 4px 0px #00000040;
  background: ${(props: IStyleProps) => {
    if (props.green) return `${Theme.pallete.QUINARY}`
    if (props.secondary) return `${Theme.pallete.TERTIARY}`
    return `${Theme.pallete.SECONDARY}`
  }};
  cursor: ${(props: IStyleProps) => {
    if (props.disabled) return ''
    return 'pointer'
  }};
  .title {
    color: ${Theme.pallete.PRIMARY};
    font-size: ${(props: IStyleProps) => {
      if (props.small) return '14px'
      return '16px'
    }};
    font-style: normal;
    font-weight: 700;
    text-align: left;
    line-height: ${(props: IStyleProps) => {
      if (props.small) return '16.41px'
      return '18.75px'
    }};
    letter-spacing: 1%;
    margin: 0 0 22px 25px;
  }
  .iconClickable {
    filter: ${(props: IStyleProps) => {
      if (props.disabled) return `${Theme.icons.PRIMARY}`
      if (props.secondary) return `${Theme.icons.PRIMARY}`
      if (props.green) return `${Theme.icons.PRIMARY}`
      return `${Theme.icons.PRIMARY}`
    }};
    max-width: 24px;
    height: 24px;
    margin: 0 0 12px 25px;
  }
  .editIcon {
    filter: ${(props: IStyleProps) => {
      if (props.disabled) return `${Theme.icons.PRIMARY}`
      if (props.secondary) return `${Theme.icons.PRIMARY}`
      if (props.green) return `${Theme.icons.PRIMARY}`
      return ''
    }};
    max-width: 12px;
    height: 12px;
    margin: ${(props: IStyleProps) => {
      if (props.small) return '0 0 22px 175px'
      return '30px 0 22px 274px'
    }};
  }
  input {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    flex-wrap: wrap;
    border: none;
    max-width: ${(props: IStyleProps) => {
      if (props.small) return '138px'
      return '197px'
    }};
    background: ${(props: IStyleProps) => {
      if (props.green) return `${Theme.pallete.QUINARY}`
      if (props.secondary) return `${Theme.pallete.TERTIARY}`
      return `${Theme.pallete.SECONDARY}`
    }};
    cursor: ${(props: IStyleProps) => {
      if (props.disabled) return ''
      return 'pointer'
    }};
    font-size: 18px;
    font-style: normal;
    font-weight: 300;
    text-align: left;
    line-height: 21.09px;
    vertical-align: text-bottom;
    margin: 0 0 12px 12px;
    ::placeholder {
      color: ${(props: IStyleProps) => {
        if (props.disabled) return `${Theme.pallete.SECONDARY}`
        return `${Theme.pallete.TERTIARY}`
      }};
    }
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  p {
    width: 16px;
    max-width: 16px;
    height: 18px;
    max-height: 18px;
    color: ${Theme.pallete.TERTIARY};
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    text-align: left;
    line-height: 18px;
    margin: ${(props: IStyleProps) => {
      if (props.small) return '0 0 22px 175px'
      return '30px 0 22px 274px'
    }};
  }
`
