/* eslint-disable consistent-return */
/* eslint-disable no-shadow */
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd'
import { InvestCard, KanbanCard } from 'components'
import { Icon } from 'assets/index'
import { KanbanColumn, KanbanContainer } from './style'

interface IKanbanProps {
  large?: boolean
  data: any
  setData(_event?: object): any
  investCard?: boolean
  onClick?: any
}

export default function KanbanComponent(props: IKanbanProps) {
  const { data, setData, investCard, onClick } = props

  const onDragEnd = (result: any) => {
    if (!result.destination) return

    const { source, destination } = result
    const sourceIndex = parseInt(source.droppableId, 10)
    const destinationIndex = parseInt(destination.droppableId, 10)

    if (!data[sourceIndex].moveable) return
    if (!data[sourceIndex].moveable?.includes(destinationIndex)) return

    if (sourceIndex !== destinationIndex) {
      const sourceCol = data[sourceIndex]
      const destinationCol = data[destinationIndex]

      const sourceTask = [...sourceCol.cardList]
      const destinationTask = [...destinationCol.cardList]

      const [removed] = sourceTask.splice(source.index, 1)
      destinationTask.splice(destination.index, 0, removed)

      data[sourceIndex].cardList = sourceTask
      data[destinationIndex].cardList = destinationTask

      setData(data)
    }
  }

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <KanbanContainer>
        {data.map((column: any) => (
          <Droppable key={column.id + 1} droppableId={String(column.id + 1)}>
            {(provided) => (
              <KanbanColumn
                {...provided.droppableProps}
                ref={provided.innerRef}
                {...props}
              >
                <div className="column-header">
                  <img
                    className="icon-kanban"
                    src={Icon(
                      column.icon === 'default.png'
                        ? 'clock.svg'
                        : column.icon.replace('png', 'svg')
                    )}
                    alt="card icon"
                  />
                  <span className="column-name">
                    {column.name}: {column.cardList.length}
                  </span>
                </div>
                <div className="kanban-card-list">
                  {column.cardList.map((card: any, index: any) => (
                    <Draggable
                      key={card.id}
                      draggableId={card.id}
                      index={index}
                    >
                      {(provided, snapshot) => (
                        <div
                          className="kanban-card-container"
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={{
                            ...provided.draggableProps.style,
                            opacity: snapshot.isDragging ? '0.5' : '1',
                          }}
                        >
                          {investCard ? (
                            <InvestCard
                              item={{
                                id: card.id,
                                chart: card.chart,
                                coloredChartTitle: card.coloredChartTitle,
                                blackChartTitle: card.blackChartTitle,
                                color: card.color,
                                min: card.min,
                                colorTags: card.colorTags,
                                icon: card.icon,
                                createdAt: card.createdAt,
                                name: card.name,
                                image: card.image,
                                guarantee: card.guarantee,
                                buttonText: card.buttonText,
                                opacity: card.opacity,
                              }}
                              i={1}
                              onClick={onClick}
                              legend={'Investimento mínimo'}
                            />
                          ) : (
                            <KanbanCard
                              specialistName="Fernando Lemos"
                              name="Nome do cliente"
                              column="Venc. em 60 dias: 2"
                              color="#ffffff"
                              background="#FDAB3D"
                              tags={[]}
                              onClick={onClick}
                            />
                          )}
                        </div>
                      )}
                    </Draggable>
                  ))}
                  {provided.placeholder}
                </div>
              </KanbanColumn>
            )}
          </Droppable>
        ))}
      </KanbanContainer>
    </DragDropContext>
  )
}
