import styled from 'styled-components'
import { Theme } from 'styles/Themes'

export default styled.label`
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background: #ccc;
  border: solid 3px ${Theme.pallete.TERTIARY};

  margin-right: 8px;

  .edit-user-icon {
    display: flex;
    justity-content: center;
    position: absolute;

    img {
      width: 20px;
      height: 20px;
    }

    input {
      display: none;
    }

    &:hover {
      opacity: 0.9;
    }
  }

  .user-icon {
    position: absolute;
    width: 48px;
    height: 48px;
    border-radius: 10px;

    &:hover {
      opacity: 0.1;
    }
  }
`
