import styled from 'styled-components'
import { Theme } from 'styles/Themes'

export const Container = styled.div`
  display: flex;
  flex-direction: column;

  width: 100%;

  gap: 32px 70px;

  .juridico-title-container {
    display: flex;
    align-items: center;
  }

  .juridico-title {
    color: #000;

    /* H1 - Títulos */
    font-family: Roboto;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    margin-right: 6px;
  }

  .juridico-subtitle {
    color: #000;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
  }

  .juridico-subtitle2 {
    color: #000;

    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  .span-1 {
    color: var(--Preto-Principal, #1c1d22);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px; /* 116.667% */
    text-decoration-line: underline;

    margin-bottom: 16px;
  }

  .span-2 {
    color: var(--cinza-6-mdio-9-e, #9e9e9e);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;

    margin-bottom: 16px;
  }

  .recomendations {
    color: var(--cinza-7-cinza-novo-75, #757575);
    font-family: Inter;
    font-size: 8px;
    font-style: normal;
    font-weight: 700;
    line-height: 12px; /* 150% */
  }

  .recomendations-item {
    color: var(--cinza-5-mdio-bd, #bdbdbd);
    font-family: Inter;
    font-size: 8px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px; /* 150% */
  }

  .span-3 {
    color: var(--Blue-02-1, #3e788a);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px; /* 116.667% */
  }

  .doc-container {
    display: flex;
    flex-wrap: wrap;

    width: 70%;

    gap: 32px 70px;
  }
`
