import styled from 'styled-components'
import { Theme } from 'styles/Themes'

export const Container = styled.div`
  display: flex;
  flex-direction: column;

  margin-left: 32px;

  .captacao-subtitle {
    margin-bottom: 20px;
    height: 17px;

    color: #868686;

    font-family: ${Theme.fontFamily.PRIMARY};
    font-size: 14px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    letter-spacing: 0.14px;
  }

  .captacao-title {
    height: 23px;
    margin-bottom: 30px;

    color: #000;

    font-family: ${Theme.fontFamily.PRIMARY};
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }

  .captacao-container {
    display: flex;

    gap: 48px;
  }

  .container-kanban {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 16px;
  }
`
