import { createGlobalStyle } from 'styled-components'

export default createGlobalStyle`
  * {
    margin: 0;
    padding: 0;
    font-family: Roboto;
    box-sizing: border-box;
  }

	body {
    height: 100%;
		background: #fdfdfd;
    color: #1c1d22;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
	}

  a {
    display: flex;
    justify-content: center;
    text-decoration: none;
  }
`
