import styled from 'styled-components'
import { Theme } from 'styles/Themes'

export default styled.div`
  height: 100%;

  font-family: ${Theme.fontFamily.PRIMARY};

  color: ${Theme.pallete.SECONDARY};

  background: ${Theme.pallete.PRIMARY};

  max-width: 335px;
  max-height: 391px;

  display: flex;
  flex-direction: column;
  align-items: center;

  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: center;

  .login-company-logo {
    max-width: 210px;
    max-height: 40px;
    margin-bottom: 30px;
  }

  .login-title {
    max-height: 23px;
    margin-bottom: 30px;
  }

  .login-form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .login-subtitle {
    width: 65%;
    margin-bottom: 30px;
    color: ${Theme.pallete.SECONDARY};
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 24px;
  }
`
