import styled from 'styled-components'
import { Theme } from 'styles/Themes'

interface IStyleProps {
  small?: boolean
  green?: boolean
  secondary?: boolean
  disabled?: boolean
}

export const Label = styled.label`
  display: flex;

  width: 315px;
  height: 142px;

  border-radius: 5px;
  background: ${Theme.pallete.SECONDARY};

  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.25);

  .resume-card-picture-container-right {
    display: flex;
    justify-content: flex-end;

    width: 45%;
  }

  .resume-card-picture-container-left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 27px 0px 30px 30px;

    width: 55%;
  }

  .resume-card-picture-img {
    position: relative;
    top: 1px;
    left: 0;

    margin-right: 12px;

    background-color: ${Theme.pallete.SECONDARY3};

    border: solid 10.2px ${Theme.pallete.SECONDARY3};

    width: 125px;
    height: 125px;
    border-radius: 10px;
  }

  .resume-card-picture-icon {
    width: 24px;
    heitght: 24px;
  }

  .resume-card-span {
    color: ${Theme.pallete.PRIMARY3};
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.16px;
  }

  .resume-card-input {
    display: none;
  }
`
