import styled from 'styled-components'
import { Theme } from 'styles/Themes'

interface ISelectCard {
  checked?: boolean
}

export default styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  background: ${(props: ISelectCard) => {
    if (props.checked) return `${Theme.pallete.TERTIARY}`
    return `${Theme.pallete.SECONDARY}`
  }};
  width: 180px;
  height: 142px;
  padding: 62px 24px 32px 24px;
  border-radius: 5px;
  box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.25);
  cursor: pointer;

  .sc-icon-content {
    display: flex;
    align-items: center;
  }

  .sc-icon {
    max-width: 24px;
    max-height: 24px;
  }

  .sc-title {
    color: ${Theme.pallete.PRIMARY};
    font-family: ${Theme.fontFamily.SECONDARY};
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
  }

  .sc-selected-icon {
    display: flex;
    position: relative;
    bottom: 40px;
    left: 125px;
    max-width: 17px;
    max-height: 15.78px;
  }
`
