import styled from 'styled-components'
import { Theme } from 'styles/Themes'

interface IActionCard {
  tag?: string
}

export default styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  background: ${Theme.pallete.TERTIARY};

  width: 180px;
  height: 142px;
  padding: ${(props: IActionCard) => {
    if (props.tag) return '62px 0px 32px 24px'
    return '79px 0px 32px 24px'
  }};

  border-radius: 5px;
  box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.25);
  cursor: pointer;

  .ac-icon {
    max-width: 24px;
    max-height: 24px;
  }

  .ac-title {
    color: ${Theme.pallete.PRIMARY};
    font-family: ${Theme.fontFamily.SECONDARY};
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
  }

  .ac-tag {
    display: inline-flex;
    justify-content: center;
    align-items: center;

    gap: 10px;
    position: relative;
    bottom: 40px;
    left: 85px;

    max-width: 57px;
    max-height: 17px;

    border-radius: 5px;

    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.1px;

    padding: 2px 6px;

    color: ${Theme.pallete.SECONDARY};
    background: ${Theme.pallete.QUINARY};
  }
`
