import styled from 'styled-components'
import { Theme } from 'styles/Themes'

interface IStyleProps {
  secondary?: boolean
}

export default styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 72px;
  max-width: 315px;

  input:focus ~ span {
    opacity: 1;
  }

  .password-icon {
    position: absolute;
    top: 16px;
    right: 16px;

    width: 16px;
    height: 16px;

    filter: ${Theme.icons.PRIMARY};
  }
`
