import styled from 'styled-components'
import { Theme } from 'styles/Themes'

export default styled.label`
  display: flex;
  align-items: center;
  justify-content: space-between;

  font-size: 14px;
  font-weight: 400;
  line-height: 20px;

  margin-right: 0;
  height: 48px;
  width: 315px;
  max-width: 315px;
  padding: 12px 16px;

  box-sizing: border-box;

  border-radius: 5px;
  border: 1px dashed #cccccc;

  background: #fdfdfd;

  &:hover {
    background: #8f888133;
  }
  &:disabled {
    &::placeholder {
      color: #fff;
    }
    background: lightGray;
    border: 0;
  }

  .ui-img {
    position: relative;
    width: 24px;
    height: 24px;
    filter: ${Theme.icons.SECONDARY};
  }

  .ui-input {
    display: none;
  }

  .upload-text {
    color: #757575;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .upload-info {
    color: #cccccc;
    text-align: right;
    font-family: Inter;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
  }
`
