import {
  ActionCard,
  InfoCard,
  Modal,
  SearchBarComponent,
  SecurityConfirm,
  Table,
} from 'components/index'
import { Icon } from 'assets/index'
import { useContext, useEffect, useState } from 'react'
import { IPerson, IResponse } from 'interfaces'
import { UserContext } from 'context'
import useFetch from 'hooks/useFetch'
import { Container } from './style'
import NovoColaborador from './NovoColaborador'
import ResumoColaborador from './ResumoColaborador'
import Comission from '../Captacao/Comission'

const modalNewGestor: any = [
  [
    <NovoColaborador />,
    'Gestão de colaboradores > Novo Colaborador',
    (): void => {
      console.log('avançar 1')
    },
    (): void => {
      console.log('voltar 1')
    },
  ],
  [
    <ResumoColaborador />,
    'Gestão de colaboradores > Novo Colaborador > Resumo',
    (): void => {
      console.log('avançar 2')
    },
    (): void => {
      console.log('voltar 2')
    },
  ],
  [
    <SecurityConfirm />,
    '',
    (): void => {
      console.log('avançar 3')
    },
    (): void => {
      console.log('voltar 3')
    },
  ],
]

const modalComission: any = [
  [
    <Comission />,
    'Gestão de colaboradores > Novo Colaborador',
    (): void => {
      console.log('avançar 1')
    },
    (): void => {
      console.log('voltar 1')
    },
  ],
  [
    <div>2</div>,
    'Gestão de colaboradores > Novo Colaborador',
    (): void => {
      console.log('avançar 2')
    },
    (): void => {
      console.log('voltar 2')
    },
  ],
]

export default function Colaboradores() {
  const [data, setData] = useState<IPerson[]>([])
  const [filter, setFilter] = useState<string>('')
  const [modalData, setModalData] = useState<any>([])
  const { showModal, setShowModal, setGestor } = useContext(UserContext)

  const api = useFetch('/gestor')

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response: IResponse = await api.fetch('get')
        if (response.fail) throw new Error(response.errors.message)

        setData(response.data.data)
        console.log('data', data)
      } catch (err: any) {
        console.log('err', err)
      }
    }

    fetchData()
  }, [])

  const filteredData =
    filter.length > 0
      ? data.filter(
          (row) =>
            row.name.toLowerCase().includes(filter.toLowerCase()) ||
            row.email.toLowerCase().includes(filter.toLowerCase())
        )
      : data

  const openNovoColaborador = async () => {
    setModalData(modalNewGestor)
    await setGestor({
      name: '',
      document: '',
      email: '',
      cellphone: '',
      level: [0, 0, 0, 0, 0, 0],
      profilePic: '',
    })
    setShowModal(true)
  }

  const openNewComission = () => {
    setModalData(modalComission)
    setShowModal(true)
  }

  return (
    <Container>
      <span className="colaboradores-subtitle">Gestão de colaboradores</span>
      <h1 className="colaboradores-title">Gestão de colaboradores</h1>
      <div className="colaboradores-cards-container">
        <InfoCard icon={Icon('users.svg')} value={`${data.length}`}>
          Número de colaboradores
        </InfoCard>
        <ActionCard
          icon={Icon('trophy.svg')}
          value={'Comissionamento'}
          onClick={openNewComission}
        />
        <ActionCard
          icon={Icon('plus.svg')}
          value={'Novo colaborador'}
          onClick={openNovoColaborador}
        />
      </div>
      <div className="colaboradores-container-content">
        <div className="colaboradores-container-filter">
          <SearchBarComponent
            onChange={(e: any) => setFilter(e.target.value)}
            large
          />
        </div>
      </div>

      <Table data={filteredData} />
      <Modal showModal={showModal} setShowModal={setShowModal}>
        {modalData}
      </Modal>
    </Container>
  )
}
