import { useContext, useRef, useState } from 'react'
import { Form } from '@unform/web'
import { DefaultButton, Input, Loading, Feedback } from 'components/index'
import useFetch from 'hooks/useFetch'
import { UserContext } from 'context/userContext'
import { IResponse } from 'interfaces'
import Div from './style'

export default function SecurityConfirm() {
  const { gestor, loading, setLoading, setShowModal } = useContext(UserContext)
  const [password, setPassword] = useState<string>('')
  const [showFeedback, setShowFeedback] = useState<boolean>(false)
  const formRef = useRef<HTMLFormElement | any>(null)

  const api = useFetch()

  const validatePassword = async () => {
    try {
      const response: IResponse = await api.fetch(
        'post',
        { password },
        '/gestor/verify-password'
      )
      if (response.fail) throw new Error(response.errors.message)
      return response.status
    } catch (err: any) {
      console.log('err', err)
      return null
    }
  }

  const createGestor = async () => {
    try {
      const response: IResponse = await api.fetch('post', gestor, '/gestor')
      if (response.fail) throw new Error(response.errors.message)
      return response.status
    } catch (err: any) {
      console.log('err', err)
      return null
    }
  }

  const submit = async () => {
    try {
      setLoading(true)
      const passwordConfirmed: number | null | undefined =
        await validatePassword()

      if (passwordConfirmed !== 201) return

      const status = await createGestor()
      console.log('status', status)

      if (status !== 201) return

      setShowModal(false)
      setLoading(false)
      setShowFeedback(true)
    } catch (err: any) {
      console.log('err', err)
      setLoading(false)
    }
  }

  if (showFeedback) return <Feedback feedback={'Colaborador adicionado!'} />
  if (loading) return <Loading />

  return (
    <Div>
      <div className="security-confirm-container">
        <h1 className="security-confirm-h1">Confirmação de segurança</h1>
        <span className="security-confirm-span">
          Digite aqui a sua senha para confirmar a operação.
        </span>
        <Form
          onSubmit={() => console.log('submit')}
          className="login-form"
          autoComplete="off"
          ref={formRef}
        >
          <Input
            secondary
            onChange={(e: any) => setPassword(e.target.value)}
            name="password"
            type="password"
            placeholder="Senha"
            value={password}
          />
          <DefaultButton onClick={submit} secondary>
            Confirmar
          </DefaultButton>
        </Form>
      </div>
    </Div>
  )
}
