import { MouseEvent } from 'react'
import { LogoutButton, MenuButton } from './style'

interface IMenuButtonProps {
  icon: any
  children?: string
  onClick?: (_e: MouseEvent<HTMLElement>) => void
  name: string
  active?: boolean
  extended: boolean
  to?: string
  logoutButton?: boolean
}

export default function MenuComponent({
  icon,
  children,
  extended,
  active,
  to,
  logoutButton,
  ...rest
}: IMenuButtonProps) {
  return logoutButton ? (
    <LogoutButton extended={extended} active={active} {...rest} to={to || ''}>
      <span>{children}</span>
      <img src={icon} alt="menuIcon" />
    </LogoutButton>
  ) : (
    <MenuButton extended={extended} active={active} {...rest} to={to || ''}>
      <img src={icon} alt="menuIcon" />
      <span>{children}</span>
    </MenuButton>
  )
}
