import { MouseEvent } from 'react'
import Card from './style'

interface IActionCard {
  tag?: string
  icon: any
  value: string
  onClick: (_e: MouseEvent<HTMLElement>) => void
}

export default function ActionCard(props: IActionCard) {
  const { value, icon, tag } = props

  return (
    <Card {...props}>
      {tag && <span className="ac-tag">{tag}</span>}
      <img className="ac-icon" src={icon} alt="card icon" />
      <div>
        <span className="ac-title">{value}</span>
      </div>
    </Card>
  )
}
