import { Icon } from 'assets/index'
import { useRef } from 'react'
import { Form } from '@unform/web'
import { MaskedInput } from 'components/index'
import { Container } from './style'

export default function Comission() {
  const formRef = useRef<HTMLFormElement | any>(null)

  return (
    <Container>
      <Form ref={formRef} onSubmit={() => console.log('submit')}>
        <div>
          <h1 className="new-colaborator-title">Política de comissionamento</h1>
          <div className="new-colaborator-subtitle">
            <img src={Icon('trophy.svg')} alt="subtitle icon" />
            <h4>Meta de equipe</h4>
          </div>
          <div className="new-colaborator-container">
            <MaskedInput
              smallPad
              disabled={false}
              type="text"
              handleChange={(e: any) => console.log(e)}
              placeholder="Meta de venda"
              name="meta"
              defaultValue={''}
            />
            <MaskedInput
              smallPad
              disabled={false}
              type="text"
              handleChange={(e: any) => console.log(e)}
              placeholder="Porcentagem de comissão"
              name="meta"
              defaultValue={''}
            />
          </div>
          <div className="new-colaborator-subtitle">
            <img src={Icon('profile.svg')} alt="subtitle icon" />
            <h4>Comissão de Vendas (Individual)</h4>
          </div>
          <div className="new-colaborator-container">
            <MaskedInput
              smallPad
              disabled={false}
              type="text"
              handleChange={(e: any) => console.log(e)}
              placeholder="R$"
              name="meta"
              defaultValue={''}
            />
            <span>até</span>
            <MaskedInput
              smallPad
              disabled={false}
              type="text"
              handleChange={(e: any) => console.log(e)}
              placeholder="R$"
              name="meta"
              defaultValue={''}
            />
            <MaskedInput
              smallPad
              disabled={false}
              type="text"
              handleChange={(e: any) => console.log(e)}
              placeholder="Porcentagem de comissão"
              name="meta"
              defaultValue={''}
            />
          </div>
          <div className="new-colaborator-subtitle">
            <img src={Icon('users.svg')} alt="subtitle icon" />
            <h4>Gestor da carteira</h4>
          </div>
          <div className="new-colaborator-container">
            <MaskedInput
              smallPad
              disabled={false}
              type="text"
              handleChange={(e: any) => console.log(e)}
              placeholder="Porcentagem de comissão"
              name="meta"
              defaultValue={''}
            />
          </div>
        </div>
      </Form>
    </Container>
  )
}
