/* eslint-disable import/extensions */
import { useContext, useEffect, useRef, useState } from 'react'
import { UserContext } from 'context/userContext'
import { ColorPicker, InvestCard, Slider, UploadImg } from 'components/index'
import { adjustColor } from 'helper/secondColor'
import { Form } from '@unform/web'
import { Container } from './style'

export default function ResumoCaptacao() {
  const { setAllowNextPage, setNewToken } = useContext(UserContext)
  const [color, setColor] = useState<any>('')
  const formRef = useRef<HTMLFormElement | any>(null)

  useEffect(() => {
    setAllowNextPage(false)
    const validateInputs = async () => {
      try {
        // await schemaNewGestor.validate('gestor', { abortEarly: false })
        setAllowNextPage(true)
      } catch (err) {
        console.log(err)
        setAllowNextPage(false)
      }
    }
    validateInputs()
  }, [])

  return (
    <Container>
      <h1 className="new-token-title">Criar Layout da Nova Captação</h1>
      <div className="new-token-container">
        <div className="new-token-container-left">
          <div className="new-token-subtitle">
            <h4>Cores</h4>
          </div>
          <div className="color-picker-container">
            <ColorPicker
              // initColor={newCaption.color}
              getColor={(newColor: string) => {
                setColor(newColor)
                setNewToken((c: any) => ({
                  ...c,
                  newColor,
                  secondColor: adjustColor(newColor, 0.8, 0.4),
                }))
              }}
            />
            <ColorPicker
              placeholder="Cor Secundária gerada"
              disabled
              initColor={adjustColor(color, 0.8, 0.4)}
              getColor={() => console.log('second')}
            />
          </div>

          <div className="color-picker-container">
            <div>
              <h4 className="new-token-subtitle">Imagem</h4>
              <span className="span-1">Selecione um arquivo</span>
              <span className="span-2"> ou arraste e solte aqui</span>
              <Form ref={formRef} onSubmit={() => console.log('submit')}>
                <UploadImg>Upload de imagem</UploadImg>
              </Form>
              <span className="recomendations">Recomendações do upload:</span>
              <span className="recomendations-item">
                Arquivo de imagem: JPG ou PNG
              </span>
              <span className="recomendations-item">
                Tamanho da imagem: 300px x 130px
              </span>
            </div>

            <div>
              <h4 className="new-token-subtitle">Opacidade da imagem</h4>
              <span className="span-3">Deslize</span>
              <span className="span-2">
                para ajustar o nível de opacidade da imagem
              </span>
              <Slider getStep={(e: any) => console.log(e)} />
            </div>
          </div>
        </div>
        <div className="new-token-container-right">
          <span className="preview">Preview da captação no app Neoin</span>
          <InvestCard
            item={{
              id: 'asdasdsasd',
              chart: [{ rate: 0.75, title: null, color: '#FFC600' }],
              coloredChartTitle: '75%',
              blackChartTitle: 'Captados',
              color: '#FFC600',
              min: '1.000,00',
              colorTags: ['Pre-fixado', '24 meses', 'IPCA + 15% a.a.'],
              icon: 'OETokenBasicPink',
              createdAt: 2023,
              name: 'CCB sapiensbank 1',
              image: 'default_header.png',
              guarantee: 'FGC-CDB',
              buttonText: 'Investir',
              opacity: 60,
            }}
            i={1}
            onClick={() => console.log('testewertetet')}
            legend={'Investimento mínimo'}
          />
        </div>
      </div>
    </Container>
  )
}
