import styled from 'styled-components'

interface ISearchBar {
  large?: boolean
}

export const Container = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;

  width: ${(props: ISearchBar) => {
    if (props.large) return '422px'
    return '180px'
  }};
  height: 40px;

  padding: 8px;
  gap: 10px;
  border-radius: 5px;
  border: 1px solid var(--preto-principal, #1c1d22);

  overflow: hidden;

  .sb-img {
    max-width: 16px;
    max-height: 16px;
  }
`

export const SearchBar = styled.input`
  transition: width 1s;

  width: 100%;

  outline: none;
  border: none;
  background-image: none;
  background-color: transparent;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  box-sizing: border-box;

  color: #d4d4d4;
  font-size: 13px;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0.01em;
  text-align: left;

  ::placeholder {
    color: #d4d4d4;
  }
`
