import styled from 'styled-components'

export const Container = styled.div`
  display: flex;
  flex-direction: column;

  width: 100%;

  .new-token-container {
    display: flex;
    flex-wrap: wrap;

    width: 70%;

    gap: 4px 24px;
  }

  .new-token-title {
    color: #000;

    margin-bottom: 32px;

    font-family: Roboto;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }

  .new-token-subtitle {
    display: flex;
    align-items: center;

    width: 100%;

    margin-bottom: 16px;

    color: #070707;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.16px;
  }

  .new-token-div-juros {
    display: flex;
    justify-content: space-around;

    width: 316px;
  }

  .new-token-plus {
    margin-top: 12px;
    color: var(--Cinza-claro, #9c948c);
    text-align: center;

    /* A - Link ou legendas - Light */
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */
  }
`
