import styled from 'styled-components'
import { Theme } from 'styles/Themes'

interface IButtonProps {
  isBack?: boolean
  isAdvance?: boolean
  allowNextPage?: boolean
}

export const ButtonNext = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  gap: 20px;

  margin-top: 24px;
  max-width: ${(props: IButtonProps) => {
    if (props.isBack || props.isAdvance) return '120px'
    return '230px'
  }};
  height: 50px;

  color: ${Theme.pallete.PRIMARY};
  font-family: ${Theme.fontFamily.PRIMARY};

  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0.75px;
  text-align: center;

  .btn-next-icon {
    width: 24px;
    height: 24px;
    cursor: pointer;
  }

  .btn-next-circle {
    padding: 13px;
    background: ${(props: IButtonProps) => {
      if (!props.allowNextPage) return `${Theme.pallete.QUATERNARY}`
      return `${Theme.pallete.TERTIARY}`
    }};
    border-radius: 50%;
    width: 50px;
    height: 50px;
  }

  .btn-back-circle {
    padding: 13px;
    background: ${Theme.pallete.TERTIARY};
    border-radius: 50%;
    width: 50px;
    height: 50px;
  }
`
