import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { Theme } from 'styles/Themes'

interface IMenuProps {
  extended: boolean
}

interface IButtonStyleProps {
  active?: boolean
  extended: boolean
}

interface ILogoutButtonStyleProps {
  active?: boolean
  extended: boolean
  logoutButton?: boolean
}

export const Menu = styled.div`
  transition: width 1s;
  z-index: 90;
  width: ${(props: IMenuProps) => {
    return `${props.extended ? '255px' : '76px'}`
  }};
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  position: fixed;

  white-space: nowrap;

  background: ${Theme.pallete.PRIMARY};

  .logo {
    overflow: hidden;

    box-sizing: border-box;
    max-width: ${(props: IMenuProps) => {
      return `${props.extended ? '152.72px' : '32px'}`
    }};
    max-height: 32px;
    margin: 14px 0px 14px 24px;
    display: flex;
  }

  .extendButton {
    max-width: 24px;
    max-height: 24px;
    transition: transform 1s;
    transform: rotate(180deg);
    transform: ${(props: IMenuProps) => props.extended && 'none'};
    z-index: 99;
    position: absolute;
    right: -12px;
    top: 18px;
    cursor: pointer;
  }
`

export const MenuButton = styled(Link)`
  display: flex;
  justify-content: flex-start;
  transition: width 1s;

  cursor: pointer;
  height: 60px;

  font-family: ${Theme.fontFamily.PRIMARY};
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 0.13px;

  color: ${(props: IButtonStyleProps) => {
    if (props.active) return `${Theme.pallete.SECONDARY}`
    return `${Theme.pallete.QUATERNARY}`
  }};

  background: ${(props: IButtonStyleProps) => {
    if (props.active) return `${Theme.pallete.TERTIARY}`
    return `${Theme.pallete.PRIMARY}`
  }};

  border: 1px solid ${Theme.pallete.PRIMARY4};
  border-right: 0;

  overflow: hidden;
  width: ${(props: IButtonStyleProps) => (props.extended ? '254px' : '75px')};

  &:hover {
    background: ${Theme.pallete.PRIMARY4};

    color: ${Theme.pallete.SECONDARY};

    img {
      filter: ${Theme.icons.TERTIARY};
    }
  }

  img {
    margin: 20px 0px 0px 30px;
    max-width: 16px;
    max-height: 20px;
    filter: ${(props: IButtonStyleProps) => {
      if (props.active) return `${Theme.icons.TERTIARY}`
      return `${Theme.icons.PRIMARY}`
    }};
  }

  span {
    transition: width 1s, opacity 1s;
    opacity: ${(props: ILogoutButtonStyleProps) =>
      props.extended ? '1' : '0'};
    width: ${(props: IButtonStyleProps) => (props.extended ? '159px' : '0px')};
    margin: 22px 0px 0px 20px;
`

export const LogoutButton = styled(Link)`
  display: flex;
  justify-content: center;

  transition: width 1s;

  cursor: pointer;
  height: 60px;

  font-family: ${Theme.fontFamily.PRIMARY};
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 0.13px;

  color: ${(props: ILogoutButtonStyleProps) => {
    if (props.active) return `${Theme.pallete.PRIMARY}`
    return `${Theme.pallete.QUATERNARY}`
  }};

  background: ${(props: ILogoutButtonStyleProps) => {
    if (props.active) return `${Theme.pallete.TERTIARY}`
    return `${Theme.pallete.PRIMARY}`
  }};

  border: ${(props: ILogoutButtonStyleProps) => {
    if (props.extended) return ''
    return `1px solid ${Theme.pallete.PRIMARY4};`
  }};
  border-right: 0;

  overflow: hidden;
  width: ${(props: ILogoutButtonStyleProps) =>
    props.extended ? '254px' : '75px'};

  &:hover {
    background: ${Theme.pallete.PRIMARY4};
  
    color: ${Theme.pallete.SECONDARY};
  
    img {
      filter: ${Theme.icons.TERTIARY};
    }
  }

  img {
    margin: ${(props: ILogoutButtonStyleProps) =>
      props.extended ? '20px 0px 0px 10px' : '20px 35px 0px 10px'};
    max-width: 16px;
    max-height: 20px;
    filter: ${Theme.icons.TERTIARY};
  }

  span {
    transition: width 1s, margin 1s, opacity 1s;
    opacity: ${(props: ILogoutButtonStyleProps) =>
      props.extended ? '1' : '0'};
    text-align: right;
    width: ${(props: ILogoutButtonStyleProps) =>
      props.extended ? '45px' : '0px'};
    margin: ${(props: ILogoutButtonStyleProps) =>
      props.extended ? '22px 0px 0px 20px' : '22px 0px 0px 30px'};
`
