import {
  InfoCard,
  ActionCard,
  SelectCard,
  DefaultButton,
  Modal,
  SearchBarComponent,
  KanbanCard,
  ResumeInputCard,
  Select,
  ResumeSelectCard,
  InvestCard,
  ColorPicker,
  Slider,
} from 'components/index'
import ErrorMsg from 'components/ErrorMsg'
import { useContext, useState, useRef } from 'react'
import { UserContext } from 'context/userContext'
import { Icon } from 'assets/index'
import { Form } from '@unform/web'
import { Theme } from 'styles/Themes'
import Div from './style'

const tags = [
  {
    style: 'GREEN',
    value: 'Ativo',
  },
  {
    style: 'WHITE',
    value: 'pf',
  },
  {
    style: 'BLACK',
    value: 'Valor: R$ 58,33',
  },
  {
    style: 'YELLOW',
    value: 'Invest Test 1',
  },
]

export default function Demo() {
  const [checkCard, setCheckCard] = useState(true)
  const { showModal, setShowModal } = useContext(UserContext)
  const formRef = useRef<HTMLFormElement | any>(null)

  const modalConfig: any = [
    [
      <div>1</div>,
      '1',
      (): void => {
        console.log('avançar 1')
      },
      (): void => {
        console.log('voltar 1')
      },
    ],
    [
      <div>2</div>,
      '2',
      (): void => {
        console.log('avançar 2')
      },
      (): void => {
        console.log('voltar 2')
      },
    ],
    [
      <div>3</div>,
      '3',
      (): void => {
        console.log('avançar 3')
      },
      (): void => {
        console.log('voltar 3')
      },
    ],
  ]

  return (
    <Div>
      <InfoCard small icon={Icon('money.svg')} value="7">
        Texto bem grande para testar o tamanho do card
      </InfoCard>
      <InfoCard icon={Icon('money.svg')} value="7">
        Número de colaboradores
      </InfoCard>
      <InfoCard large icon={Icon('money.svg')} value="7">
        Número de colaboradores
      </InfoCard>
      <InfoCard small secondary icon={Icon('money.svg')} value="7">
        Número
      </InfoCard>
      <ActionCard
        icon={Icon('money.svg')}
        value="Novo colaborador"
        onClick={() => console.log('clicou no card')}
        tag="Assinado"
      />
      <ActionCard
        icon={Icon('money.svg')}
        value="Novo colaborador"
        onClick={() => console.log('clicou no card')}
      />
      <SelectCard
        icon={Icon('money.svg')}
        value="Dashboard"
        onClick={() => setCheckCard((current) => !current)}
        checked={checkCard}
      />
      <DefaultButton
        $loading={false}
        onClick={() => console.log('clicou no botao')}
      >
        Entrar
      </DefaultButton>
      <DefaultButton
        $loading={false}
        disabled
        onClick={() => console.log('clicou no botao')}
      >
        Entrar
      </DefaultButton>
      <DefaultButton
        $loading={false}
        onClick={() => setShowModal((curr) => !curr)}
      >
        Abrir modal
      </DefaultButton>
      <SearchBarComponent />
      <ErrorMsg>Este campo é obrigatório</ErrorMsg>
      <Modal showModal={showModal} setShowModal={setShowModal}>
        {modalConfig}
      </Modal>
      <KanbanCard
        specialistName="Fernando Lemos"
        name="Nome do cliente"
        column="Venc. em 60 dias: 2"
        color="#ffffff"
        background="#FDAB3D"
        tags={tags}
      />
      <ResumeInputCard
        icon="profile.svg"
        placeholder={'Placeholder'}
        names="name"
      >
        Teste
      </ResumeInputCard>
      <ResumeSelectCard>teste</ResumeSelectCard>
      <Form ref={formRef} onSubmit={() => console.log('submit')}>
        <Select options={[{ label: 'AC', value: 'ac' }]} name="z" />
      </Form>
      <InvestCard
        item={{
          id: 'asdasdsasd',
          chart: [{ rate: 0.75, title: null, color: '#FFC600' }],
          coloredChartTitle: '75%',
          blackChartTitle: 'Captados',
          color: '#FFC600',
          min: '1.000,00',
          colorTags: ['Pre-fixado', '24 meses', 'IPCA + 15% a.a.'],
          icon: 'OETokenBasicPink',
          createdAt: 2023,
          name: 'CCB sapiensbank 1',
          image: 'default_header.png',
          guarantee: 'FGC-CDB',
          buttonText: 'Investir',
          opacity: 60,
        }}
        i={1}
        onClick={() => console.log('testewertetet')}
        legend={'Investimento mínimo'}
      />
      <ColorPicker
        getColor={(e: any) => console.log(e)}
        initColor={Theme.pallete.TERTIARY}
      />
      <Slider />
    </Div>
  )
}
