import { Icon } from 'assets/index'
import Div from './style'

interface IErrorMsgProps {
  icon?: any
  children: JSX.Element[] | JSX.Element | string
}

export default function ErrorMsg(props: IErrorMsgProps) {
  const { children } = props
  return (
    <Div {...props}>
      <img className="em-icon" src={Icon('alert-circle.svg')} alt="card icon" />
      <span className="em-span">{children}</span>
    </Div>
  )
}
