import styled from 'styled-components'
import { Theme } from 'styles/Themes'

interface IInfoCard {
  secondary?: boolean
  tertiary?: boolean
  filter?: string
  width?: number
}

export default styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: end;

  background: ${(props: IInfoCard) => {
    if (props.secondary) return `${Theme.pallete.TERTIARY}`
    if (props.tertiary) return `${Theme.pallete.QUINARY}`
    return `${Theme.pallete.SECONDARY}`
  }};
  width: ${(props: IInfoCard) => {
    if (props.width) return `${props.width}px`
    return `auto`
  }};
  height: 142px;
  padding: 56px 32px 32px 32px;
  border-radius: 5px;
  box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.25);

  .ic-icon {
    max-width: 24px;
    max-height: 24px;
    filter: ${(props: IInfoCard) => {
      if (props.filter) return `${props.filter}`
      return ``
    }};
  }

  .ic-content {
    display: flex;
    align-items: center;
  }

  .ic-value {
    color: ${Theme.pallete.PRIMARY};
    font-family: ${Theme.fontFamily.SECONDARY};
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.22px;
    white-space: nowrap;

    width: 100%;
    margin-left: 10px;
  }

  .ic-title {
    color: ${Theme.pallete.PRIMARY};
    font-family: ${Theme.fontFamily.SECONDARY};
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
    white-space: nowrap;

    margin-top: 12px;
  }
`
