import styled from 'styled-components'
import { Theme } from 'styles/Themes'

interface IInputProps {
  empty?: boolean
  error?: string
  secondary?: boolean
}

export const Span = styled.span`
  position: absolute;
  left: 11px;
  top: -11px;

  color: ${(props: IInputProps) =>
    props.secondary ? Theme.pallete.PRIMARY4 : Theme.pallete.QUATERNARY};
  font-family: ${Theme.fontFamily.SECONDARY};
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

  height: 20px;

  padding: 2px 6px;
  border-radius: 10px;

  background: ${(props: IInputProps) =>
    props.secondary ? Theme.pallete.SECONDARY : Theme.pallete.PRIMARY};

  opacity: ${(props: IInputProps) => (props.empty ? '0' : '1')};
`

export const Input = styled.input`
  background: ${(props: IInputProps) =>
    props.secondary ? Theme.pallete.SECONDARY : Theme.pallete.PRIMARY};

  font-family: ${Theme.fontFamily.SECONDARY};

  color: ${(props: IInputProps) =>
    props.secondary ? Theme.pallete.PRIMARY4 : Theme.pallete.QUATERNARY};

  border-radius: 5px;

  width: 315px;
  height: 48px;

  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;

  -webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px red inset !important;
  }

  -webkit-text-fill-color: ${(props: IInputProps) =>
    props.secondary
      ? Theme.pallete.PRIMARY4
      : Theme.pallete.QUATERNARY} !important;

  padding: 0px 16px;

  border: ${(props: IInputProps) => {
    if (props.error) return `1px solid ${Theme.pallete.SENARY}`
    if (props.secondary) return `1px solid ${Theme.pallete.PRIMARY4}`
    return `1px solid ${Theme.pallete.QUATERNARY}`
  }};

  &:hover {
    background: ${(props: IInputProps) => {
      if (props.secondary) return `${Theme.pallete.SECONDARY3}`
      return `${Theme.pallete.PRIMARY4}`
    }};
    color: ${Theme.pallete.SECONDARY3};
  }

  &:focus {
    &::placeholder {
      color: transparent;
      opacity: 0;
    }
  }

  &:disabled {
    &::placeholder {
      color: #fff;
    }
    background: lightGray;
    border: 0;
  }

  &:placeholder-shown {
    text-overflow: ellipsis;
  }
`

export const InputError = styled.div`
  font-family: ${Theme.fontFamily.SECONDARY};
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: start;
  padding: 5px 20px;
  width: 215px;
  text-overflow: ellipsis;
  overflow: hidden;
  color: ${Theme.pallete.SENARY};
`
