import styled from 'styled-components'

interface IStyleProps {
  investCard?: boolean
}

export const KanbanColumn = styled.div`
  display: flex;
  flex-direction: column;
  justity-content: center;
  align-items: center;

  gap: 5px;
  margin: 0px 5px 15px;
  padding: 20px 30px;

  background-color: #f9f9f9;

  height: auto;
  width: ${(props: IStyleProps) => {
    if (props.investCard) return `364px`
    return `270px`
  }};

  overflow: auto;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #c4c4c4;
    border-radius: 50px;
    border: none;
  }

  .column-name {
    display: flex;
    align-items: center;
    align-self: center;

    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 15px;
    letter-spacing: 0.01em;
    text-align: left;

    color: #070707;
  }

  .kanban-card-list {
    display: flex;
    flex-direction: column;

    gap: 5px;
  }

  .kanban-card-container {
    height: auto;
  }
`

export const KanbanContainer = styled.div`
  display: flex;
  justify-content: flex-start;

  overflow: auto;

  width: auto;

  margin: 0 0 20px 0;
  &::-webkit-scrollbar {
    height: 8px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #c4c4c4;
    border-radius: 50px;
    border: none;
  }

  .icon-kanban {
    margin-right: 10px;
    width: 24px;
    height: 24px;
  }

  .column-header {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 26px;
    margin-bottom: 20px;
  }
`
