import { InputContainer } from 'styles'
import { useState, useEffect, useRef } from 'react'
import { useField } from '@unform/core'
import { IMask } from 'react-imask'
import { InputError, MaskedInput, Span } from './style'

interface IInputProps {
  rounded?: boolean
  small?: boolean
  xsmall?: boolean
  large?: boolean
  icon?: boolean
  error?: string
  marginRight?: number
  smallPad?: boolean
  name: string
  handleChange?: any
  initialError?: string
  placeholder?: string
  disabled?: boolean
  defaultValue?: string | number | undefined
  type: string
}

const getCurrent18 = () => {
  const now = new Date()
  return now.getFullYear() - 18
}

export default function MaskedInputComponent({
  name,
  initialError,
  handleChange,
  type,
  placeholder,
  defaultValue,
  ...rest
}: IInputProps) {
  const { fieldName, registerField, error } = useField(name)
  const inputRef = useRef<any>(null)
  const [Erro, setErro] = useState(initialError)
  const [value, setValue] = useState<string>('')

  const showInput = (tp: any) => {
    switch (tp) {
      case 'money':
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            ref={inputRef}
            mask="R$ num"
            blocks={{
              num: {
                mask: Number,
                thousandsSeparator: '.',
              },
            }}
            defaultValue={defaultValue}
            {...rest}
          />
        )
      case 'text':
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            ref={inputRef}
            mask=""
            defaultValue={defaultValue}
            {...rest}
          />
        )
      case 'cpf':
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            mask="000.000.000-00"
            ref={inputRef}
            defaultValue={defaultValue}
            {...rest}
          />
        )
      case 'rg':
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            mask="00.000.000"
            ref={inputRef}
            defaultValue={defaultValue}
            {...rest}
          />
        )
      case 'cnpj':
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            ref={inputRef}
            mask="00.000.000/0000-00"
            defaultValue={defaultValue}
            {...rest}
          />
        )
      case 'cell':
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            ref={inputRef}
            mask="(00) 00000-0000"
            defaultValue={defaultValue}
            {...rest}
          />
        )
      case 'cep':
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            ref={inputRef}
            mask="00000-000"
            defaultValue={defaultValue}
            {...rest}
          />
        )
      case 'birthday':
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            ref={inputRef}
            mask="d/m/Y"
            blocks={{
              d: {
                mask: IMask.MaskedRange,
                from: 1,
                to: 31,
                maxLength: 2,
              },
              m: {
                mask: IMask.MaskedRange,
                from: 1,
                to: 12,
                maxLength: 2,
              },
              Y: {
                mask: IMask.MaskedRange,
                from: 1900,
                to: getCurrent18(),
              },
            }}
            defaultValue={defaultValue}
            {...rest}
          />
        )
      case 'jurosaa':
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            ref={inputRef}
            mask="num% \a.\a."
            eager
            blocks={{
              num: {
                mask: Number,
                scale: 2,
                signed: false,
                from: 0,
                to: 100,
              },
            }}
            defaultValue={defaultValue}
            {...rest}
          />
        )
      default:
        return (
          <MaskedInput
            onChange={(e: any) => handleChange(e)}
            placeholder={placeholder}
            className="masked-input"
            mask="0000000000"
            ref={inputRef}
            defaultValue={defaultValue}
            {...rest}
          />
        )
    }
  }

  useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: 'value',
    })
  }, [registerField, fieldName])

  useEffect(() => {
    if (error) {
      setErro(error)
    }
  }, [error])

  return (
    <InputContainer>
      {showInput(type)}
      <Span empty={defaultValue === ''}>{placeholder}</Span>
      {Erro && <InputError>{Erro}</InputError>}
    </InputContainer>
  )
}
