// import { LoadGif } from 'components'
import { MouseEvent } from 'react'
import Button from './style'

interface IButtonProps {
  border?: boolean
  small?: boolean
  disabled?: boolean
  children: JSX.Element[] | JSX.Element | string
  onClick?: (_e: MouseEvent<HTMLElement>) => void
  $loading?: boolean | string
  icon?: any
  secondary?: boolean
}

export default function DefaultButton(props: IButtonProps) {
  const { $loading, children, icon } = props
  return (
    <Button {...props}>
      {icon ? (
        <img className="default-btn-img" src={icon} alt="select icon" />
      ) : null}
      {children}
    </Button>
  )
}
