import { useStorage } from 'hooks'
import { createContext, Dispatch, SetStateAction } from 'react'
import { jwtDecode } from 'jwt-decode'

interface AuthContextType {
  setToken: Dispatch<SetStateAction<string>>
  token: string
  signin: (_newToken: string, _callback: VoidFunction) => void
  signout: (_callback: VoidFunction) => void
  user: {
    status: any
    issuer: string
    audience: string
    user: {
      client: string
      key: string
      level: string
      profilePic: any
      sector: string
      status: string
    }
    iat: number
    exp: number
  }
}

interface IdecodedUser {
  payload: {
    issuer: string
    audience: string
    user: {
      client: string
      key: string
      level: string
      profilePic: any
      sector: string
      status: string
    }
    iat: number
    exp: number
  }
}

export const AuthContext = createContext<AuthContextType>(null!)

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [token, setToken] = useStorage('Token')
  const [user, setUser] = useStorage('User')

  const signin = (newToken: any, callback: VoidFunction) => {
    const decodedUser: IdecodedUser = jwtDecode(newToken)
    setUser(decodedUser.payload)
    callback()
  }

  const signout = (callback: VoidFunction) => {
    setToken(null)
    setUser(null)
    callback()
  }

  const value = {
    token,
    setToken,
    signin,
    signout,
    user,
  }

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>
}
