import { picDashboard } from 'assets/Icons'
import { Icon } from 'assets/index'
import { useRef } from 'react'
import { Form } from '@unform/web'
import { FileInput } from '..'
import Label from './style'

/* eslint-disable spaced-comment */
interface IEditPictureProps {
  name: string
}

export default function EditPicture(props: IEditPictureProps) {
  const { name } = props
  const formRef = useRef<HTMLFormElement | any>(null)
  return (
    <Label htmlFor="editPicture">
      <div className="edit-user-icon">
        <img src={Icon('edit.svg')} alt="edit user icon" />
        <Form ref={formRef} onSubmit={() => console.log('submit')}>
          <FileInput id="editPicture" name={name} />
        </Form>
      </div>
      <img className="user-icon" src={picDashboard} alt="user pic" />
    </Label>
  )
}
