import styled from 'styled-components'
import { Theme } from 'styles/Themes'

export default styled.div`
  display: flex;
  height: auto;
  flex-wrap: wrap;
  gap: 24px;
  padding: 0px 0px 0px 60px;

  .user-info {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    height: 54px;
  }

  .greeting {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    margin-bottom: 8px;
    margin-top: 4px;

    color: #1c1d22;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .greeting-container {
    display: flex;
    flex-direction: column;
  }

  .user-name {
    color: #ffc600;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }
`
