import { useContext, useEffect, useRef } from 'react'
import { Form } from '@unform/web'
import { MaskedInput, Select } from 'components/index'
import { UserContext } from 'context/userContext'
import * as Yup from 'yup'
import { Container } from './style'

const selectOptions = {
  accountList: [
    { label: 'Captação aberta', value: [] },
    { label: 'Captação fechada', value: [] },
  ],
  type: [
    { label: 'Cotas imobiliárias', value: 'FIAT' },
    { label: 'Empreendimento', value: 'COIN' },
    { label: 'Token', value: 'RENTAL' },
  ],
  guaranteeList: [
    { label: 'SPE', value: 'SPE' },
    { label: 'SCP', value: 'SCP' },
    { label: 'Aval dos sócios', value: 'Aval dos sócios' },
    { label: 'Ativos', value: 'Ativos' },
    { label: 'Não possui', value: 'Não possui' },
  ],
  icoDays: [
    { label: '7 dias', value: 7 },
    { label: '15 dias', value: 15 },
    { label: '30 dias', value: 30 },
    { label: '60 dias', value: 60 },
    { label: '90 dias', value: 90 },
    { label: 'Full', value: 'full' },
  ],
  deadline: [
    { label: '1 mês', value: 1 },
    { label: '15 meses', value: 15 },
    { label: '24 meses', value: 24 },
    { label: '36 meses', value: 36 },
    { label: '48 meses', value: 48 },
  ],
  recurrencyType: [
    { label: 'Diária', value: 'Diária' },
    { label: 'Mensal', value: 'Mensal' },
    { label: 'Trimestral', value: 'Trimestral' },
    { label: 'Anual', value: 'Anual' },
    { label: 'No vencimento', value: 'No vencimento' },
  ],
  principalLiquidityType: [
    { label: 'Diária', value: 'Diária' },
    { label: 'Anual', value: 'Anual' },
    { label: 'No vencimento', value: 'No vencimento' },
  ],
  paymentType: [
    { label: 'Pré fixado', value: 'PRE' },
    { label: 'Pós fixado', value: 'POS' },
    { label: 'Híbrido', value: 'HYBRID' },
  ],
  indiceId: [
    { label: 'IPCA', value: 'IPCA' },
    { label: 'IGP-M', value: 'IGP-M' },
    { label: 'CDI', value: 'CDI' },
  ],
  IRType: [
    { label: 'Descontado na fonte', value: 'Descontado na fonte' },
    { label: 'Não descontado na fonte', value: 'Não descontado na fonte' },
    { label: 'Isento', value: 'Isento' },
  ],
  cotizationIn: [
    { label: 'No dia', value: 0 },
    { label: '1 dia', value: 1 },
    { label: '5 dias', value: 5 },
    { label: '10 dias', value: 10 },
    { label: '15 dias', value: 15 },
    { label: '30 dias', value: 30 },
    { label: '60 dias', value: 60 },
    { label: '90 dias', value: 90 },
  ],
  cotizationOut: [
    { label: 'Até 30 dias', value: 30 },
    { label: 'Até 60 dias', value: 60 },
    { label: 'Até 90 dias', value: 90 },
  ],
}

export default function NovaCaptacao() {
  const { setAllowNextPage, newToken, setNewToken } = useContext(UserContext)
  const formRef = useRef<HTMLFormElement | any>(null)

  useEffect(() => {
    setAllowNextPage(false)
    const validateInputs = async () => {
      try {
        // await schemaNewGestor.validate('gestor', { abortEarly: false })
        setAllowNextPage(true)
      } catch (err) {
        console.log(err)
        setAllowNextPage(false)
      }
    }
    validateInputs()
  }, [])

  return (
    <Container>
      <Form ref={formRef} onSubmit={() => console.log('submit')}>
        <div>
          <h1 className="new-token-title">Nova captação</h1>
          <div className="new-token-subtitle">
            <h4>Informações do produto</h4>
          </div>
          <div className="new-token-container">
            <MaskedInput
              smallPad
              type="text"
              handleChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  name: e.target.value,
                }))
              }}
              placeholder="Nome completo do produto"
              name="name"
              defaultValue={newToken?.name}
            />
            <MaskedInput
              smallPad
              type="text"
              handleChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  fonetic: e.target.value,
                }))
              }}
              placeholder="Nome resumido"
              name="fonetic"
              defaultValue={newToken?.fonetic}
            />
            <MaskedInput
              smallPad
              type="text"
              handleChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  tick: e.target.value,
                }))
              }}
              placeholder="Sigla do produto"
              name="tick"
              defaultValue={newToken?.tick}
            />
            <Select
              name="accountList"
              options={selectOptions.accountList}
              placeholder="Modalidade de captação"
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  accountList: e.value,
                }))
              }}
            />
            <Select
              placeholder="Tipo de investimento"
              name="type"
              options={selectOptions.type}
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  type: e.value,
                }))
              }}
            />
            <Select
              placeholder="Garantia"
              small
              options={selectOptions.guaranteeList}
              name="guaranteeList"
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  guaranteeList: e.value,
                }))
              }}
            />
          </div>
          <div className="new-token-subtitle">
            <h4>Informações da captação</h4>
          </div>
          <div className="new-token-container">
            <MaskedInput
              smallPad
              type="money"
              handleChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  price: e.target.value,
                }))
              }}
              placeholder="Preço da cota"
              name="price"
              defaultValue={`${newToken?.price}`}
            />
            <MaskedInput
              smallPad
              type="number"
              handleChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  maxSupply: e.target.value,
                }))
              }}
              placeholder="Total de cotas"
              name="maxSupply"
              defaultValue={newToken?.maxSupply}
            />
            <MaskedInput
              smallPad
              disabled
              type="money"
              placeholder="Meta da captação"
              name="maxSupply2"
              defaultValue={''}
            />
            <MaskedInput
              smallPad
              type="money"
              handleChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  minInvest: e.target.value,
                }))
              }}
              placeholder="Investimento mínimo"
              name="minInvest"
              defaultValue={newToken?.minInvest}
            />
            <Select
              placeholder="Período de captação"
              name="icoDays"
              options={selectOptions.icoDays}
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  icoDays: e.value,
                }))
              }}
            />
          </div>
          <div className="new-token-subtitle">
            <h4>Rentabilidade</h4>
          </div>
          <div className="new-token-container">
            <Select
              placeholder="Prazo do contrato"
              name="deadline"
              options={selectOptions.deadline}
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  deadline: e.value,
                }))
              }}
            />
            <Select
              placeholder="Pagamento dos juros"
              name="recurrencyType"
              options={selectOptions.recurrencyType}
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  recurrencyType: e.value,
                }))
              }}
            />
            <Select
              placeholder="Liquidez"
              name="principalLiquidityType"
              options={selectOptions.principalLiquidityType}
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  principalLiquidityType: e.value,
                }))
              }}
            />
            <Select
              placeholder="Indexador"
              name="paymentType"
              options={selectOptions.paymentType}
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  paymentType: e.value,
                }))
              }}
            />
            <div className="new-token-div-juros">
              <Select
                small
                placeholder="Índice"
                name="indiceId"
                options={selectOptions.indiceId}
                onChange={(e: any) => {
                  setNewToken((oldValue: any) => ({
                    ...oldValue,
                    indiceId: e.value,
                  }))
                }}
              />
              <span className="new-token-plus">+</span>
              <MaskedInput
                smallPad
                small
                type="jurosaa"
                handleChange={(e: any) => console.log(e)}
                placeholder="Juros a.a."
                name="minInvestimento"
                defaultValue={''}
              />
            </div>

            <Select
              placeholder="Imposto de renda"
              name="IRType"
              options={selectOptions.IRType}
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  IRType: e.value,
                }))
              }}
            />
            <Select
              placeholder="Período de cotização de entrada"
              name="cotizationIn"
              options={selectOptions.cotizationIn}
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  cotizationIn: e.value,
                }))
              }}
            />
            <Select
              placeholder="Período de cotização de saída"
              name="cotizationOut"
              options={selectOptions.cotizationOut}
              onChange={(e: any) => {
                setNewToken((oldValue: any) => ({
                  ...oldValue,
                  cotizationOut: e.value,
                }))
              }}
            />
            <Select
              placeholder="Cotização de saída rentabilizada"
              name={'cotizacaoSaidaRentabilizada'}
            />
          </div>
        </div>
      </Form>
    </Container>
  )
}
