import { MouseEvent, useContext, useEffect, useState } from 'react'
import { IResponse } from 'interfaces'
import useFetch from 'hooks/useFetch'
import { UserContext } from 'context/userContext'
import { Icon } from 'assets/index'
import { ActionCard, InfoCard, Loading } from 'components/index'
import { Theme } from 'styles/Themes'
import { Container } from './style'

export default function DetalhesCaptacao() {
  const { selectedCardId, loading, setLoading } = useContext(UserContext)
  const [data, setData] = useState<any>({})

  const api = useFetch(`/token/${selectedCardId}`)

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true)
        const response: IResponse = await api.fetch('get')
        if (response.fail) throw new Error(response.errors.message)

        console.log('data', response.data.data)
        setData(response.data.data)
        setLoading(false)
      } catch (err: any) {
        console.log('err', err)
        setLoading(false)
      }
    }

    fetchData()
  }, [])

  if (loading) return <Loading />
  return (
    <Container>
      <h1 className="detalhes-captacao-title">
        {data?.title ? data.title : 'Neostyle Carrão 1/2023'}
      </h1>
      <div className="status-card-captacao">
        <img
          className="status-card-captacao-img"
          src={Icon('lock_open.svg')}
          alt="status icon"
        />
        <span>Abertas</span>
      </div>
      <h4 className="detalhes-captacao-subtitle">Informações do produto</h4>
      <div className="status-card-container-cards">
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('lock_open.svg')}
          value={'Aberta'}
        >
          Modalidade de captação
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('lock_open.svg')}
          value={'CCB'}
        >
          Tipo de investimento
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('shield.svg')}
          value={'CCB-FGC'}
        >
          Garantia
        </InfoCard>
      </div>
      <h4 className="detalhes-captacao-subtitle">Informações da captação</h4>
      <div className="status-card-container-cards">
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('cash_min.svg')}
          value={'R$ 1.000,00'}
        >
          Investimento mínimo
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('cash.svg')}
          value={'R$ 1,00'}
        >
          Preço da cota
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('cash.svg')}
          value={'100'}
        >
          Total de cotas
        </InfoCard>
        <InfoCard icon={Icon('cash.svg')} value={'88'} secondary>
          Cotas disponíveis
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('calendar.svg')}
          value={'Full'}
        >
          Período de captação
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('aim.svg')}
          value={'R$ 1.000.000,00'}
        >
          Meta de captação
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('dollar.svg')}
          value={'R$ 90.000,00'}
        >
          Captados
        </InfoCard>
        <InfoCard icon={Icon('profile.svg')} value={'780'} secondary>
          Investidores
        </InfoCard>
      </div>
      <h4 className="detalhes-captacao-subtitle">Datas</h4>
      <div className="status-card-container-cards">
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('calendar.svg')}
          value={'21/09/2022'}
        >
          Data de emissão
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('calendar.svg')}
          value={'D + 30'}
        >
          Inicio do Pagamento
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('calendar.svg')}
          value={'D + 450'}
        >
          Último pagamento
        </InfoCard>
        <InfoCard icon={Icon('calendar.svg')} value={'D + 450'} secondary>
          Retorno do principal
        </InfoCard>
      </div>
      <h4 className="detalhes-captacao-subtitle">Rentabilidade</h4>
      <div className="status-card-container-cards">
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('money.svg')}
          value={'R$ 0,00'}
        >
          Rentabilidade paga
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('money.svg')}
          value={'R$ 5.000,00'}
        >
          Projeção de pagamento mensal
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('money.svg')}
          value={'R$ 160.000,00'}
        >
          Rentabilidade projetada
        </InfoCard>
      </div>
      <h4 className="detalhes-captacao-subtitle">Condições da captação</h4>
      <div className="status-card-container-cards">
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('document_clock.svg')}
          value={'48 meses'}
        >
          Prazo do contrato
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('money.svg')}
          value={'Mensal'}
        >
          Pagamento dos juros
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('money.svg')}
          value={'No vencimento'}
        >
          Liquidez
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('invest.svg')}
          value={'Pré fixado'}
        >
          Indexador
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('percentage.svg')}
          value={'IPCA +'}
        >
          Índice
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('percentage.svg')}
          value={'6,20%'}
        >
          Juros a.a.
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('percentage.svg')}
          value={'Isento'}
        >
          Imposto de renda
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('login.svg')}
          value={'D + 1'}
        >
          Período de cotização de entrada
        </InfoCard>
        <InfoCard
          filter={Theme.icons.SECONDARY}
          icon={Icon('login.svg')}
          value={'D + 0'}
        >
          Período de cotização de saída
        </InfoCard>
      </div>
      <h4 className="detalhes-captacao-subtitle">Jurídico</h4>
      <div className="status-card-container-cards">
        <ActionCard
          icon={Icon('file.svg')}
          value="Contrato SCP"
          onClick={() => console.log('clicou no card')}
        />
        <ActionCard
          icon={Icon('file.svg')}
          value="Alvará"
          onClick={() => console.log('clicou no card')}
        />
        <ActionCard
          icon={Icon('file.svg')}
          value="CNPJ da SPE"
          onClick={() => console.log('clicou no card')}
        />
        <ActionCard
          icon={Icon('file.svg')}
          value="Registro do imóvel"
          onClick={() => console.log('clicou no card')}
        />
        <ActionCard
          icon={Icon('file.svg')}
          value="Tempo aditivo"
          onClick={() => console.log('clicou no card')}
        />
      </div>
    </Container>
  )
}
