import { InputContainer } from 'styles'
import { ChangeEvent, useState, useEffect, useRef, useContext } from 'react'
import { useField } from '@unform/core'
import { Icon } from 'assets/index'
import { Input, Span, InputError } from './style'

interface IInputProps {
  type: string
  error?: string
  name: string
  placeholder?: string
  disabled?: boolean
  value?: string
  maxLength?: number
  onChange?: (_e: ChangeEvent<HTMLInputElement>) => void
  initialError?: string
  secondary?: boolean
}

export default function InputComponent(props: IInputProps) {
  const { name, maxLength, type, placeholder, value, secondary } = props
  const [showPassword, setShowPassword] = useState<boolean>(false)

  const handleChangeState = () => {
    setShowPassword((oldState) => {
      return !oldState
    })
  }

  return (
    <InputContainer>
      <Input
        placeholder={placeholder}
        {...props}
        maxLength={maxLength}
        name={name}
        type={showPassword ? 'text' : type}
        defaultValue=""
        value={value}
      />
      <Span secondary={secondary} empty={value === ''}>
        {placeholder}
      </Span>
      {type === 'password' ? (
        <img
          className="password-icon"
          src={showPassword ? Icon('cell_phone.svg') : Icon('lock_open.svg')}
          alt="password lock icon"
          onClick={handleChangeState}
        />
      ) : null}
    </InputContainer>
  )
}
