import { RefObject } from 'react'
import { pic } from 'assets/Icons'
import { KanbanCard, Tag } from './style'

interface ICardProps {
  name: string
  tags: any
  specialistName: string
  disabled?: boolean
  column: string | number
  color: string
  background: string
  destinationList?: string[]
  cardData?: { [key: string]: string }
  onClick?(_event?: object): any
  ref?: (
    _instance: HTMLDivElement | null
  ) => void | RefObject<HTMLDivElement> | null
  isDragging?: boolean
}

export default function KanbanCardComponent(props: ICardProps) {
  const { name, tags, specialistName } = props

  return (
    <KanbanCard {...props}>
      <span className="kc-name">{`${name?.substring(0, 16)}${
        String(name).length > 16 ? '...' : ''
      }`}</span>

      <div className="kc-tags-container">
        {tags?.map((tag: any) => {
          return (
            <Tag background={tag.style} color="#ffffff" className="tag">
              {tag.value}
            </Tag>
          )
        })}
      </div>

      <span className="kc-name">Especialista:</span>

      <div className="kc-specialist">
        <img className="kc-icon" src={pic} alt="profile" />
        <span className="kc-span">{specialistName}</span>
      </div>
    </KanbanCard>
  )
}
