import { IGestor, IKanbanColumns, IToken } from 'interfaces'
import { createContext, Dispatch, SetStateAction, useState } from 'react'

interface IUserContextType {
  loading: boolean
  setLoading: Dispatch<SetStateAction<boolean>>
  extended: boolean
  setExtended: Dispatch<SetStateAction<boolean>>
  showModal: boolean
  setShowModal: Dispatch<SetStateAction<boolean>>
  showCarousel: boolean
  setShowCarousel: Dispatch<SetStateAction<boolean>>
  kanbanData: IKanbanColumns[]
  setKanbanData: any
  searchParam: string
  setSearchParam: Dispatch<SetStateAction<string>>
  showModalNewClient: boolean
  setShowModalNewClient: Dispatch<SetStateAction<boolean>>
  preview?: string
  preview64?: string
  setPreview: Dispatch<SetStateAction<string>>
  setPreview64: Dispatch<SetStateAction<string>>
  gestor?: IGestor
  setGestor?: any
  allowNextPage: boolean
  setAllowNextPage: Dispatch<SetStateAction<boolean>>
  newToken?: IToken
  setNewToken?: any
  selectedCardId?: any
  setSelectedCardId?: any
}

export const UserContext = createContext<IUserContextType>(null!)

export function UserProvider({ children }: { children: React.ReactNode }) {
  const [loading, setLoading] = useState(false)

  // MENU
  const [extended, setExtended] = useState(true)

  // MODAL Cliente
  const [showModal, setShowModal] = useState(false)
  const [allowNextPage, setAllowNextPage] = useState(true)

  // CAROUSEL
  const [showCarousel, setShowCarousel] = useState<boolean>(false)

  // Kanban
  const [kanbanData, setKanbanData] = useState([])
  const [searchParam, setSearchParam] = useState('')
  const [selectedCardId, setSelectedCardId] = useState('')

  // MODAL Cliente
  const [showModalNewClient, setShowModalNewClient] = useState(false)

  // imagens
  const [preview, setPreview] = useState('')
  const [preview64, setPreview64] = useState('')

  // register gestor
  const [gestor, setGestor] = useState<IGestor>({
    name: '',
    document: '',
    email: '',
    cellphone: '',
    level: [0, 0, 0, 0, 0, 0],
    profilePic: '',
  })

  // create token
  const [newToken, setNewToken] = useState<IToken>({
    // INFORMAÇÕES DO PRODUTO
    name: '', // Nome completo
    fonetic: '', // Nome resumido
    tick: '', // Tick
    accountList: [], // (Aberta se for vazio) Lista de clientes para investimentos personalizados
    type: '', // Tipo de captação [FIAT, COIN, RENTAL, ...]
    guaranteeList: [], // Lista de garantias

    // INFORMAÇÕES DA CAPTAÇÃO
    price: '', // Inicial price
    maxSupply: '', // Total supply (total de cotas)
    minInvest: '', // Valor mínimo para entrada
    icoDays: '', // In days - Período de lançamento

    // RENTABILIDADE
    deadline: '', // Prazo do contrato em meses
    recurrencyType: '', // Pagamento dos juros
    principalLiquidityType: '', // Data de saque do principal
    paymentType: '', // Tipo de pagamento dos juros [PRE, POS, HYBRID]
    indiceId: undefined, // Id do indexador (apenas para pós e híbrido)
    profitability: '', // Annual profitability
    IRType: '', // DEFAULT - desconto na fonte
    cotizationIn: '', // In days - Cotização de entrada
    cotizationOut: '', // In days - Cotização de saída

    // LAYOUT
    style: {
      iconList: [],
      color: [], // Primária, secundária, terciária...
      image: {
        base64: '', // Imagem do header em base64
        oppacity: '', // Opacidade da película do header
      },
    },

    // DETALHES DA CAPTAÇÃO
    details: [],
    contractList: [],

    // MISCELÂNEA
    blockchain: '', // Blockchain onde a token será criada
    hasCETIP: false, // Possui selo?
    hasCupom: false, // Possui cupom?
  })

  const value = {
    loading,
    setLoading,
    extended,
    setExtended,
    showModal,
    setShowModal,
    showCarousel,
    setShowCarousel,
    searchParam,
    setSearchParam,
    showModalNewClient,
    setShowModalNewClient,
    kanbanData,
    setKanbanData,
    preview,
    setPreview,
    preview64,
    setPreview64,
    gestor,
    setGestor,
    allowNextPage,
    setAllowNextPage,
    newToken,
    setNewToken,
    selectedCardId,
    setSelectedCardId,
  }

  return <UserContext.Provider value={value}>{children}</UserContext.Provider>
}
